//首页
<template>
  <div>
    <page :pageData="pageData"></page>
  </div>
</template>
<script>
import {mapState} from 'vuex'
  export default {
    data() {
      return {
        pageData: {
          pocHeaderData: [],
          sidebarData: [{
            visible: true,
            title: 'MES管理系统',
            chlid: [
              {
                name:'物控中心',
                router:'wk',
                chlid:[
                  {
                    name:'工艺设置',
                    router:'technology'
                  },
                  {
                    name:'产品库',
                    router:'product'
                  },
                  {
                    name:'半成品库',
                    router:'semi-manufactures'
                  },
                  {
                    name:'产品工艺',
                    router:'productProcess'
                  }
                ]
              },
              {
                name:'排程管理',
                router:'pc',
                chlid:[
                  {
                    name:'内部订单',
                    router:'internalOrder'
                  },{
                    name:'内部订单审批',
                    router:'approve'
                  },{
                    name:'工艺任务分配',
                    router:'tasking'
                  },{
                    name:'工艺任务领取',
                    router:'receive'
                  },
                  {
                    name:'任务分配记录',
                    router:'taskingRecord'
                  }
                ]
              },
              {
                name:'任务交工',
                router:'rwjg',
                chlid:[
                  {
                    name:'交工',
                    router:'jobAssignment'
                  },{
                    name:'任务交工记录',
                    router:'jobAssignmentHistory'
                  }
                ]
              },
              {
                name:'电子发图',
                router:'electronicHairMap'
              },
              {
                name:'客户管理',
                router:'customerManagement',
                chlid:[{
                    name:'查看上传资料',
                    router:'seeUpload'
                  },{
                    name:'客户评审',
                    router:'customerReview'
                  },{
                    name:'客户工艺评审',
                    router:'customerProcessReview'
                  },{
                    name:'客户上传资料',
                    router:'customerUploadData'
                  }
                ]
              },
            ]
          }]
        }
      }
    },
    computed:{
        ...mapState(['menuList','ports'])
    },
    beforeMount(){
      this.$set(this.pageData.sidebarData[0],'chlid',JSON.parse(JSON.stringify(this.menuList)))
    }
  }

</script>
<style lang="less"></style>



