<template>
    <div class="jobAssignment">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <my-table 
        :data="tableData" 
        :config="tableConfig"
        @pageChange="pageChange"
        @see = "see"
        @sure ="sure"
        @select="select"></my-table>
        <my-toast :config="toastConfig" @submit="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()">
        <div class="form_box">
            <el-form :model="detailData" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;">
                <el-row>
                  <el-col :span="8">
                        <el-form-item label="订单编号">
                            <span class="f_value">{{detailData.insideOrder}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                      <el-form-item label="产品信息">
                             <span class="f_value">{{detailData.prodMess}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                        <el-form-item label="工艺名称">
                             <span class="f_value">{{detailData.crafworkName}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="8">
                      <el-form-item label="工艺执行人">
                             <span class="f_value">{{detailData.doEmpName}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                        <el-form-item label="上传人">
                             <span class="f_value">{{detailData.sendEmp}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                      <el-form-item label="完工日期">
                             <span class="f_value">{{detailData.okDatetime}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="8">
                        <el-form-item label="确认状态">
                             <span class="f_value">{{detailData.status}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                  <!-- //waiting -->
                        <el-form-item label="上传时间">
                             <span class="f_value">{{detailData.sendDate}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                        <el-form-item label="说明">
                             <span class="f_value">{{detailData.desc}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row v-if="detailData2">
                    <div class="img_box">
                        <li class="qw_pic_box" v-for="(item, i) in detailData2.pictureUrls">
                            <img class="qw_pic" :src="item? item: emptySrc">
                            <p class="img_title">图片{{i+1}}</p>
                        </li>
                    </div>
                </el-row>
               <!--   -->
            </el-form>
            </div>
        </my-toast>
        <my-toast  :config="toastConfig2" @submit="doSure" @close="resetForm2" @cannel="toastConfig2.dialogVisible=false; resetForm2();">
            <div class="form_box">
                <el-form :model="sureForm" :rules="sureRules" ref="ruleForm2" label-width="100px" style="margin-right:30px;">
                        <!-- {{sureForm}} -->
                       <el-row>
                          <el-col :span="8">
                                <el-form-item label="订单编号">
                                    <span class="f_value">{{sureForm.insideOrder}}</span>
                                </el-form-item>
                          </el-col>
                          <el-col :span="8">
                              <el-form-item label="产品信息">
                                     <span class="f_value">{{sureForm.prodMess}}</span>
                                </el-form-item>
                          </el-col>
                          <el-col :span="8">
                                <el-form-item label="工艺名称">
                                     <span class="f_value">{{sureForm.crafworkName}}</span>
                                </el-form-item>
                          </el-col>
                        </el-row>
                        <el-row>
                          <el-col :span="8">
                              <el-form-item label="工艺执行人">
                                     <span class="f_value">{{sureForm.doEmpName}}</span>
                                </el-form-item>
                          </el-col>
                          <el-col :span="8">
                                <el-form-item label="上传人">
                                     <span class="f_value">{{sureForm.sendEmp}}</span>
                                </el-form-item>
                          </el-col>
                          <el-col :span="8">
                                <el-form-item label="完工日期" prop="okDatetime">
                                    <el-date-picker style="width:144px;"
                                      v-model="sureForm.okDatetime"
                                      type="date"
                                      placeholder="请选择日期"
                                      value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                          </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8">
                                <el-form-item label="实际工时" prop="actHours">
                                    <el-input-number :controls="false"  :min="0" :max="99999" placeholder="请输入实际工时" v-model="sureForm.actHours"></el-input-number>    
                                </el-form-item>
                            </el-col>

                            <el-col :span="16">
                                <el-form-item label="说明">
                                    <el-input v-model="sureForm.desc" placeholder="请输入说明（限50字）" maxlength="50"></el-input>    
                                </el-form-item>
                            </el-col>
                        </el-row>
                          <el-row >
                        <div class="img_box">
                             <li class="qw_pic_box" v-for="(item, i) in sureForm.pictureUrls">
                                <img class="qw_pic" :src="item? item: emptySrc" >
                                <p> <span class="img_title">图片{{i+1}}</span><span @click="delImg(i)" class="btn_del">删除</span></p>
                            </li>
                            <el-upload class="upImg"
                              :action="uploadPath"
                              list-type="picture-card"
                              :on-success="upImgSuccess"                              
                              :show-file-list="false"
                              >
                              <i class="el-icon-plus"></i>
                            </el-upload>
                        </div>
                </el-row>
                </el-form>
            </div>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
import {
    uploadPath,//上传地址
    pictMessDetail,//任务交工详情
    findByMes,//任务交工分页
    accomplish,//确认交工
    getDropOrderMes
} from './api.js'
export default {
    data(){
        return{
            testlist:[],
            imageUrl: '',
            sureForm:{
                insideOrder:"",     //内部单号
                prodNo:"",          //产品编码
                prodMess:"",        //产品信息
                crafworkName:"",    //工艺名称
                crafworkId:"",      //工艺ID            
                doEmpName:"",       //工艺执行人
                sendEmp:"",         //上传人
                okDatetime:"",      //完工日期
                pictureUrls:[],     //图片
                actHours:1,        //实际工时
                desc:"",            //交工说明
            },
            sureRules:{
                actHours:[{
                    required: true,
                    type:'number',
                    message: '此选择由最多7位整数加1位小数组成',
                    trigger: ['input','change','blur'],
                }],
                okDatetime:[{
                        required: true,
                        message: '请选择完工日期',
                        trigger: ['input','change','blur'],
                }]
            },
            uploadPath: uploadPath,
            emptySrc: 'data:image/gif;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQImWNgYGBgAAAABQABh6FO1AAAAABJRU5ErkJggg==',
            // 详情
            detailData:{},
            detailData2:{},
            //确认完工数据
            sureData:[],
            // 要删除的参数
            deleteArr:[],
            // 当前表格选中项
            selectData:[],
            // 校验
            rules:{
                
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "内部订单号",
                        field: "insideOrder",
                        filterable: true,
                        type: "select",
                        placeholder: "请输入/选择内部订单号",
                        childrens:[]
                    },
                    {
                        label: "产品信息",
                        field: "prodMess",
                        type: "input",
                        placeholder: "请输入手机号码"
                    },
                    {
                        label: "确认状态",
                        field: "isCheck",
                        type: "select",
                        placeholder: "请选择确认状态",
                        childrens:[{"name":"已完工","value":true},{"name":"制作中","value":false}]
                    }
                ]
            ],
            tableData:[],//[{insideOrder:"ZD000000001",prodMess:"棉麻|PRO000002",crafworkName:"整经01",doEmpName:"马云",okDatetime:"2018-07-25",actHours:"10",sendEmp:"张三",sendDate:"2018-07-08",status:'进行中',desc:"描述详情描述详情"}],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                loadShow:false,
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        field: "insideOrder",
                        isResize: true,
                        label: "内部订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "prodMess",
                        isResize: true,
                        label: "产品信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "crafworkName",
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "doEmpName",
                        isResize: true,
                        label: "工艺执行人",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "okDatetime",
                        isResize: true,
                        label: "完工日期",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "actHours",
                        isResize: true,
                        label: "实际工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "status",
                        isResize: true,
                        label: "确认状态",
                        align: "center",
                        type: "text"
                    },
                    {
                        width: "200px",
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'查看详情',
                                method:'see'
                            },
                            {
                                name:'确认完工',
                                showFunc:(scope,item)=>{
                                    return scope.row.status!="已完工"
                                },
                                method:'sure'
                            }
                        ]
                    }
                ]
            },
            toolbarConfig:{
                custom:[
                    {
                        icon: "plus",
                        type: "primary",
                        method: "handlerAdd",
                        name: "新增"
                    },
                    {
                        icon: "edit",
                        type: "",
                        method: "handlerEdit",
                        name: "编辑"
                    },
                    {
                        icon: "delete",
                        type: "",
                        method: "handlerDel",
                        name: "删除"
                    }
                ],
                btnStatus:[false,true,true]
            },
            toastConfig:{
                title:'查看详情',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'900px',//弹窗宽
                btnData:[
                {
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'关闭页面',//按钮名
                    method:'submit',//回调函数
                    bgColor:'dialog-bule'//按钮背景
                }
                ]
            },
            toastConfig2:{
                title:'确认完工',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'800px',//弹窗宽
                btnData:[
                {
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确认',//按钮名
                    method:'submit',//回调函数
                    bgColor:'dialog-bule'//按钮背景
                },
                {
                    disabled:false,
                    isShow:true,
                    type:'cancel',
                    name:'取消',//按钮名
                    method:'cannel',//回调函数
                    bgColor:'dialog-bule'//按钮背景
                }
                ]
            },
            form:{
                crafworkName:'',
                quartersEmp:'',
                actHours:1,
                unit:''
            },
            jobs:[],
            // 设置参数
            paramForm:{
                list:[1,2,3,4],
            },
            paramSet:{
                title:'参数设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'500px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'保存',//按钮名
                    method:'submit',//回调函数
                }]
            },
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        delImg(i){
            let tem = this.sureForm.pictureUrls;
            tem.splice(i,1);
            this.sureForm.pictureUrls=tem;
        },
        resetForm2(){
            this.$refs.ruleForm2.resetFields();
        },
        sureFormValidate(fun) {
            this.$refs['ruleForm2'].validate((valid) => {
              if (valid) {
                if (fun) {
                  fun();
                }
              } else {
                return false;
              }
            });
        },
        doSure(){
            this.sureFormValidate(()=>{
                let params={
                    insideOrder:this.sureForm.insideOrder,          //内部订单号 
                    prodNo:this.sureForm.prodNo,           //商品编号
                    crafworkId:this.sureForm.crafworkId,          //工艺id
                    actHours:this.sureForm.actHours,          //实际工时(6位数, 1位小数) 
                    okDatetime:this.sureForm.okDatetime,          //完工日期 
                    pictureUrls:this.sureForm.pictureUrls,
                    getTime:this.sureForm.getTime,
                    desc:this.sureForm.desc
                }
                accomplish({params:params,success:res=>{
                    this.toastConfig2.dialogVisible=false;
                    this.$message({
                        type:'success',
                        message:'任务交工成功'
                    })
                    this.getList()
                    setTimeout(()=>{
                        this.$refs.ruleForm2.resetFields()
                    },10)
                },Vue:this})
            })            
        },
        upImgSuccess(response, file, fileList){
          if(!this.sureForm.pictureUrls)this.sureForm.pictureUrls=[];
            this.$set(this.sureForm.pictureUrls,this.sureForm.pictureUrls.length,response.model);
            console.log("upImgSuccess",response,file,fileList);
        },
        upImgRemove(file){
            let tem=  this.sureForm.pictureUrls;
            tem.splice(tem.indexOf(file.response.model),1);
            this.sureForm.pictureUrls=tem;
        },
        upImgChange(file, fileList){
            this.sureForm.pictureUrls=[];
            for(let attr in fileList){
                if(fileList[attr].response){
                    this.sureForm.pictureUrls.push(fileList[attr].response.model)
                }
            }
        },
        select(data){
            this.selectData=data
            if(data.length==1){
                this.toolbarConfig.btnStatus=[false,false,false]
            }else if(data.length==0){
                this.toolbarConfig.btnStatus=[false,true,true]
            }else{
                this.toolbarConfig.btnStatus=[false,true,false]
            }
        },
        handlerSearch(){
            this.tableConfig.pageNo=1;
            this.tableConfig.pageSize=20;
            this.getList()
        },
        setAttr(data){
            for(let attr in data){
                if(undefined===data[attr]||(null===data[attr])||(""===data[attr])){
                    delete data[attr];
                }
            }
            return  data;
        },
        getList(){
            let params={pageSize:this.tableConfig.pageSize,pageNo:this.tableConfig.pageNo, ...this.searchData};
            params=this.setAttr(params);
            // let searchData=this.searchData;
            findByMes({params:params,success:res=>{
                if(res.items){
                    this.tableData=res.items;
                    this.tableConfig.total=res.numRows;
                }
            },Vue:this})
        },
        pageChange(){
            this.getList()
        },
        //using查看详情
        see(data){
            let that=this;
            that.detailData=data;
            // that.detailData2={};
            let params = {insideOrder: data.insideOrder,prodNo: data.prodNo,crafworkId: data.crafworkId,getTime:data.getTime}
            pictMessDetail({params:params,success: res=>{
                setTimeout(()=>{
                that.detailData2 = res;
                    that.detailData2.pictureUrlsStyle=[];
                    let tem=[];
                    for(let item of res.pictureUrls){
                        tem.push('background: url("'+ item +'") 100px 100px;')
                    }
                    that.detailData2.pictureUrlsStyle=tem;
                console.log('that.detailData2==>',that.detailData2);
                },10)
                that.toastConfig2.dialogVisible=false;
                that.toastConfig.dialogVisible=true;
            },Vue:that})
            
        },
        //using 确认完工
        sure(data){
            this.sureForm={
                insideOrder:data.insideOrder,     //内部单号
                prodNo:data.prodNo,
                prodMess:data.prodMess,        //产品信息
                crafworkName:data.crafworkName,    //工艺名称
                crafworkId:data.crafworkId,      //工艺ID            
                doEmpName:data.doEmpName,       //工艺执行人
                sendEmp:data.sendEmp,         //上传人
                okDatetime:data.okDatetime,      //完工日期
                getTime:data.getTime,
                pictureUrls:[],     //图片
                actHours:1,        //实际工时
                desc:"",     
            }
            let that=this;
            that.detailData=data;
            this.$nextTick(()=>{
                this.$refs.ruleForm2.clearValidate();
            })
            let params = {insideOrder: data.insideOrder,prodNo: data.prodNo,crafworkId: data.crafworkId,getTime:data.getTime}
            pictMessDetail({params:params,success: res=>{
                setTimeout(()=>{
                    that.sureForm.pictureUrls=res.pictureUrls;
                    // that.detailData2 = res;
                    console.log('that.ruleForm',that.ruleForm);
                },0)
               

            },Vue:that})
                that.toastConfig.dialogVisible= false;
                that.toastConfig2.dialogVisible= true;
        },
        //using获取订单信息下拉
        getDropOrderMes(){
            getDropOrderMes({params:{pageSize:10000,pageNo:1},success:res=>{
                console.log("res==>",res);
                let tem=[];
                for(let o in res){
                    tem.push({"name" : res[o].insideOrder,"value" : res[o].insideOrder});
                }

                this.searchConfig[0][0].childrens=tem;
            },Vue:this})
        }
    },
    mounted(){
        this.getDropOrderMes();
        this.getList()
    }
}
</script>
<style lang="less">
    .jobAssignment{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
    .jobAssignment .f_value{
        color:#333;
        font-size:14px;
        word-break: break-all;
    }
    .form_box{
        box-sizing:border-box;
        padding-left:30px;
    }
    .img_box{
        padding:10px 0;
        box-sizing:border-box;
        margin:0 15px; 
    }

    .qw_pic_box{
        display:inline-block;
        width:130px;
        height:auto;
        padding:0 15px;
        margin-bottom:10px;
        box-sizing:border-box;
        float:left;
    }
    .qw_pic{
        display:block;
        width:100px;
        min-height:100px;
        background:#EEE;
        height:100px;
    }
    .img_title{
        display:inline-block;
        max-width:50px;
        font-size:12px;
        margin-top:5px;
        line-height:20px;
        color:#666;
        overfollow:hidden;
        text-align:left;
    }
    .btn_del{
        float:right;
        display:inline-block;
        width:40px;
        margin-top:5px;
        height:20px;
        font-size:10px;
        text-align:center;
        line-height:20px;
        color:#FFF;
        cursor:pointer;
        background:#f56c6c;
    }
    .upImg{
        display:inline-block;
        position:relative;
        width:100px;
        height:100px;
        float:left;
    }   
    .upImg .el-upload--picture-card{
        width:100px;
        height:100px;
    }
    .upImg .el-upload--picture-card i {
        position:absolute;
        display:block;
        width:30px;
        height:30px;
        left:50%;
        top:50%;
        margin-top:-15px;
        margin-left:-15px;
    }
    .jobAssignment .el-form-item__error{
      min-width:350px;
    }
</style>




