<template>
    <div class="productLibrary">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <toolbar 
            :toolbarConfig="toolbarConfig"
            @handlerAdd="handlerAdd"
            @handlerEdit="handlerEdit"
            @handlerDel="handlerDel"
        ></toolbar>
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @select="select" @see="see"></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()" @savaAgain="savaAgain" >
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;" v-if='toastConfig.dialogVisible == true'>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="产品编码" prop="prodNo">
                            <el-input :disabled="form.id!=0" v-model="form.prodNo" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="产品名称" prop="prodName">
                            <el-input v-model="form.prodName" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="产品分类" prop="typeId">
                            <el-select style="width:100%" v-model="form.typeId" size="medium">
                            <el-option v-for="(item,index) in prodTypeList" :key="index" :label="item.typeDesc" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="规格">
                            <el-input v-model="form.norms" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="参数" >
                            <el-input v-model="form.param" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="型号">
                            <el-input v-model="form.modelDesc" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="计量单位">
                            <el-input v-model="form.unit" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                         <el-col :span="12">
                        <el-form-item label="产品状态">
                            <span>正常供货</span>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <!-- <h4>产品图片</h4>
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="margin:0 30px;" label-position="left">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="图片分类" >
                            <el-select v-model="form.name" size="medium">
                            <el-option v-for="(item,index) in imgTypeList" :key="index" :label="item.name" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-upload
                    action="https://jsonplaceholder.typicode.com/posts/"
                    list-type="picture-card"
                    :on-remove="handleRemove">
                    <i class="el-icon-plus"></i>
                </el-upload>
            </el-form> -->
        </my-toast>
        <my-toast :config="detailConfig" @cancel="detailConfig.dialogVisible=false">
            <el-form :model="viewForm" ref="ruleForm" label-width="100px" style="margin-right:30px;" >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="产品编码">
                            {{viewForm.prodNo}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="产品名称" >
                            {{viewForm.prodName}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="产品分类">
                            {{viewForm.typeDesc}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="规格">
                            {{viewForm.norms}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="参数" >
                            {{viewForm.param}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="型号">
                            {{viewForm.modelDesc}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="计量单位">
                            {{viewForm.unit}}
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            rules:{
                prodName:rule.require('string','产品名称不能为空'),
                prodNo:rule.require('string','产品编码不能为空'),
                typeId:rule.require('string','请选择产品分类'),
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "产品信息",
                        field: "prodName",
                        type: "input",
                        placeholder: "请输入产品信息"
                    },
                    {
                        label: "产品编码",
                        field: "prodNo",
                        type: "input",
                        placeholder: "请输入产品编码"
                    }
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                currentSelectArr: [],
                colConfig: [
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodNo",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品编码",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "typeDesc",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "图片分类",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "norms",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "规格",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "modelDesc",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "型号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "param",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "参数",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "unit",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计量单位",
                        align: "center",
                        type: "text"
                    },
                   {
                        fixed: false,
                        field: "prodStatus",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品状态",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "150",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'查看详情',
                                method:'see'
                            }
                        ]
                    }
                ]
            },
            toolbarConfig:{
                custom:[
                    {
                        icon: "plus",
                        type: "primary",
                        method: "handlerAdd",
                        name: "新增"
                    },
                    {
                        icon: "edit",
                        type: "",
                        method: "handlerEdit",
                        name: "编辑"
                    },
                    {
                        icon: "el-icon-search",
                        type: "primary",
                        method: "handlerDel",
                        name: "关闭产品"
                    }
                ],
                btnStatus:[false,true,true]
            },
            toastConfig:{
                title:'编辑产品',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    name:'确定并继续',
                    type:'primary',
                    method:'savaAgain',
                    bgColor:'dialog-deep-blue'
                }]
            },
            form:{
                prodName:'',
                prodNo:'',
                norms:'',
                param:'',
                modelDesc:'',
                unit:'',
                typeId:'',
                prodStatus:'',
            },
            viewForm:{},
            detailConfig:{
                title:'查看详情',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'关闭窗口',//按钮名
                    method:'cancel',//回调函数
                }]
            },
        }
    },
    computed:{
        ...mapState(['ports','prodTypeList'])
    },
    methods:{
        handlerAdd(){
             this.toastConfig.title='新增产品'
             this.toastConfig.btnData[2].isShow=true
            this.form={
                prodName:'',
                prodNo:'',
                norms:'',
                param:'',
                modelDesc:'',
                unit:'',
                typeId:'',
                id:0
            },
            this.toastConfig.dialogVisible=true
        },
        handlerEdit(){
             this.toastConfig.title='编辑产品'
            this.form=Object.assign({},this.selectData[0])
            this.toastConfig.btnData[2].isShow=false
            this.toastConfig.dialogVisible=true
        },
        handlerDel(){
            console.log(this.selectData)
            var ids=[];
            var closeProd=[];
            var hasCloseProd=false;
            var message='是否关闭'
            for(var i=0;i<this.selectData.length;i++){
                if(this.selectData[i].prodStatus=='关闭'){
                    closeProd.push(this.selectData[i].prodName)
                    hasCloseProd=true
                }else{
                    ids.push(this.selectData[i].id)
                }
            }
            if(hasCloseProd&&ids.length>0){
                message=closeProd.join(',')+'已关闭，系统已自动去除，是否继续关闭其他产品'
            }else if(ids.length<=0){
                return this.$message({
                    type: 'warning',
                    message: '请勿重复关闭!'
                });
            }
            this.$confirm(message, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http.post(
                    this.ports+'/productStorePlm/closeProdStatus',
                    {ids:ids.join(',')},
                    data=>{
                        this.getList()
                        this.$message({
                            type: 'success',
                            message: '关闭成功!'
                        });
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消关闭'
                });          
            });
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(this.form.id==0){
                        this.$http.post(
                            this.ports+'/productStorePlm/addProduct',
                            this.form,
                            data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.$message({
                                    type:'success',
                                    message: '新增成功!'
                                })
                                this.getList()
                            },
                            this
                        )
                    }else{
                        this.$http.post(
                            this.ports+'/productStorePlm/updateProduct',
                            this.form,
                            data=>{
                                this.$message({
                                    type:'success',
                                    message: '编辑成功!'
                                })
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.getList()
                            },
                            this
                        )
                    }
                    
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        savaAgain(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post(
                        this.ports+'/productStorePlm/addProduct',
                        this.form,
                        data=>{
                            this.form={
                                prodName:'',
                                prodNo:'',
                                norms:'',
                                param:'',
                                modelDesc:'',
                                unit:'',
                                typeId:'',
                                id:0
                            },
                            this.$message(
                                {
                                    type:'success',
                                    message: '新增成功!'
                                }
                            )
                            this.$refs.ruleForm.resetFields()
                            this.getList()
                        },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        select(data){
            this.selectData=data
            if(data.length==1){
                this.toolbarConfig.btnStatus=[false,false,false]
            }else if(data.length==0){
                this.toolbarConfig.btnStatus=[false,true,true]
            }else{
                this.toolbarConfig.btnStatus=[false,true,false]
            }
        },
        getList(){
            this.$http.post(
                this.ports+'/productStorePlm/pageProduct',
                {
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        pageChange(){
            this.getList()
        },
        search(){
            this.tableConfig.pageNo=1;
            this.tableConfig.pageSize=20;
            this.getList()
        },
        see(data){
            this.viewForm=Object.assign({},data)
            this.detailConfig.dialogVisible=true
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    .productLibrary{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
        h4{
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(42,147,255,1);
            line-height:30px;
            height: 30px;
            border-bottom: 1px solid #E5E5E5;
            margin: 20px;
        }
    }
</style>




