<template>
    <div class="productProcess">
        <el-row :gutter="20">
          <el-col :span="6">
              <div class="treeBox no-scroll" style="overflow:auto" >
                  <tree :readOnly="true" 
                  
                        :rootData="rootData" 
                        :props="{
                            label:'prodName',
                            isLeaf:'isLeaf'
                        }"
                        @loadNode="loadNode"
                        @nodeChange="nodeChange"></tree>
              </div>
          </el-col>
          <el-col :span="18" style="max-height: 432px; ">
              <div class="tableBox no-scroll" v-if="selectData.midProdNo" >
                  <h2>{{selectData.parentName}}/{{selectData.midProdNo}}</h2>
                  <div class="productDetail"  >
                      <span>产品编码:{{selectData.prodNo}}</span>
                      <span>产品名称:{{selectData.parentName}}</span>
                      <span>半成品编码:{{selectData.midProdNo}}</span>
                      <el-button type="primary" size="medium" @click="add" style="float:right">添加工艺</el-button>
                  </div>
                  <el-table
                    :data="tableData"
                    size="small"
                    height="400px"
                    style="width: 100%" >
                    <el-table-column
                    label="工艺名称"
                    align="center"
                    show-overflow-tooltip
                    prop="crafworkName"
                    >
                    </el-table-column>
                    <el-table-column
                    label="工艺需求"
                     show-overflow-tooltip
                    align="center"
                    prop="crafworkDesc"
                    >
                    </el-table-column>
                    <el-table-column
                    label="工艺岗位"
                     show-overflow-tooltip
                    align="center"
                    prop="quartersEmp"
                    >
                    </el-table-column>
                    <el-table-column
                    label="前置工艺"
                     show-overflow-tooltip
                    align="center"
                    prop="befCrafwork"
                    >
                    </el-table-column>
                    <el-table-column
                    label="工艺顺序"
                    align="center"
                    >
                        <template slot-scope="scope">
                            <span>顺序<span class="caret-wrapper">
                                <i :class="{'isDisabled':scope.$index==0}" class="sort-caret ascending" @click="move(scope.row,scope.$index,-1)"></i>
                                <i :class="{'isDisabled':scope.$index==tableData.length-1}" class="sort-caret descending" @click="move(scope.row,scope.$index,1)"></i></span>
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                    label="计量单位"
                    align="center"
                     show-overflow-tooltip
                    prop="unit"
                    >
                    </el-table-column>
                    <el-table-column label="操作" align="center"  width="200px">
                        <template slot-scope="scope">
                            <el-button type="text" @click="set(scope.$index,scope.row)">设置参数值</el-button>
                            <el-button type="text" @click="edit(scope.$index,scope.row)">编辑</el-button>
                            <el-button type="text" @click="del(scope.$index,scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
              </div>
              <div class="tableBox no-scroll" v-else="selectData.midProdNo">
                  <p>请先选择半成品</p>
              </div>
          </el-col>
        </el-row>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()" @savaAgain="savaAgain">
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;" v-if='toastConfig.dialogVisible == true'>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="工艺名称" prop="crafworkId">
                            <el-select style="width:100%" v-model="form.crafworkId" size="medium" @change="handlerChange">
                                <el-option  v-for="(item,index) in crafworkList" :key="index" :label="item.crafworkName" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="工艺要求" prop="crafworkDesc">
                            <el-input v-model="form.crafworkDesc" size="medium" placeholder="请输入不小于五位数的字符"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12" v-if="form.crafworkId!=''">
                        <el-form-item label="工艺岗位" >
                            {{form.quartersEmp}}
                            <!-- <el-input v-model="form.quartersEmp" placeholder=""></el-input> -->
                        </el-form-item>
                    </el-col>
                    <!-- <el-col :span="12">
                        <el-form-item label="工艺顺序" prop="carfSeq">
                            <el-input v-model="form.carfSeq" size="medium"></el-input>
                        </el-form-item>
                    </el-col> -->
                    <el-col :span="12">
                        <el-form-item label="构成数量" >
                            <el-input-number :min="1" :controls="false" v-model="form.amount" size="medium"></el-input-number>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    
                    <el-col :span="12" v-if="form.crafworkId!=''">
                        <el-form-item label="计量单位">
                            {{form.unit}}
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </my-toast>
        <my-toast :config="paramSet" @cancel="paramSet.dialogVisible=false" @submit="paramSubmit">
            <my-table 
                style="padding:0 20px;"
                :data="paramList" 
                :config="paramConfig"></my-table>
            </my-toast>
    </div>
</template>
<style lang="less">
    @colPading:8px 0;
    @theadColor:#666;
    @theadImage:#F6F6F6;
    .productProcess{
        margin-top: 20px;
        .treeBox{
            height: 600px;
            background: #fff;
            padding: 20px;
            box-sizing: border-box;
        }
        .tableBox{
            p{
                line-height: 600px;
                text-align: center;
            }
            .el-table .sort-caret.ascending{
                border-bottom-color: #2b62cf;
            }
            .el-table .sort-caret.ascending.isDisabled{
                border-bottom-color: #c0c4cc;
            }
            .el-table .sort-caret.descending{
                border-top-color: #2b62cf;
            }
            .el-table .sort-caret.descending.isDisabled{
                border-top-color: #c0c4cc;
            }
            .el-table td{
                padding:@colPading;
            }
            .el-table thead {
                color: @theadColor;
                font-weight: 500;
                background: @theadImage;
            }
            .el-table th, .el-table tr {
                background-color: rgba(0,0,0,0);
            }
            height: 600px;
            background: #fff;
            padding: 20px;
            box-sizing: border-box;
            h2{
                font-size:16px;
                font-family:MicrosoftYaHei-Bold;
                color:rgba(51,51,51,1);
                height: 40px;
                line-height: 40px;
            }
            .productDetail{
                margin-bottom: 10px;
                >span{
                    font-size:14px;
                    font-family:MicrosoftYaHei;
                    color:rgba(102,102,102,1);
                    line-height:36px;
                    height: 36px;
                    margin-right: 100px;
                }
            }
        }
    }
</style>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            // 校验
            rules:{
                crafworkId:rule.require('string','请选择工艺名称'),
                crafworkDesc:[
                     { required: true, message: '请输入工艺要求', trigger: 'blur' },
                    { min: 5, message: '请输入不小于五位数的字符', trigger: 'blur' }
                ]
            },
            crafworkList:[],
            selectData:{
                id:0
            },
            tableData:[],
            rootData:[{
                prodName:'产品列表',
                id:0
            }],
            toastConfig:{
                title:'添加工艺',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                }]
            },
            paramSet:{
                title:'参数设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                }]
            },
            paramList:[],
            paramConfig:{
                align: "center",
                isPage: false,
                colConfig: [
                    {
                        field: "paramName",
                        label: "参数名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        field: "values1",
                        isResize: true,
                        label: "值1",
                        align: "center",
                        type: "input"
                    },
                    {
                        field: "values2",
                        isResize: true,
                        label: "值2",
                        align: "center",
                        type: "input"
                    }
                ]
            },
            form:{
                crafworkId:'',
                crafworkDesc:'',
                quartersEmp:'',
                carfSeq:'',
                amount:0,
                unit:'',
            },
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        handlerChange(val){
            if(val){
                var index=this.crafworkList.findIndex(data=>data.id==val)
                console.log(this.crafworkList[index])
                this.form.quartersEmp=this.crafworkList[index].quartersEmp
                // this.form.amount=this.crafworkList[index].amount
                this.form.unit=this.crafworkList[index].unit
            }
        },
        move(data,index,key){
            var param1=Object.assign({},data)
            delete param1.createTime;
            delete param1.modifyTime;
            if(key==-1){
                var param2=Object.assign({},this.tableData[index-1])
                var carfSeq=param1.carfSeq
                param1.carfSeq=param2.carfSeq
                param2.carfSeq=carfSeq
                delete param2.createTime;
                delete param2.modifyTime;
                if(index==0){
                    return false
                }else{
                    this.$http.post_noqs(
                        this.ports+'/prodCrafworkPlm/saveOrders',
                         [param1,param2],
                        data=>{
                            this.getList()
                        },
                        this
                    )
                }
            }else if(key==1){
                 var param2=Object.assign({},this.tableData[index+1])
                var carfSeq=param1.carfSeq
                param1.carfSeq=param2.carfSeq
                param2.carfSeq=carfSeq
                delete param2.createTime;
                delete param2.modifyTime;
                if(this.tableData.length-1==index){
                    return false
                }else{
                    this.$http.post_noqs(
                        this.ports+'/prodCrafworkPlm/saveOrders',
                         [param1,param2],
                        data=>{
                            this.getList()
                        },
                        this
                    )
                }
            }
        },
        paramSubmit(){
            this.$http.post_noqs(
                this.ports+'/prodCrafworkPlm/addParam',
                this.paramList,
                data=>{
                    this.paramSet.dialogVisible=false;
                },
                this
            )
        },
        set(index,row){
            this.$http.post(
                this.ports+'/prodCrafworkPlm/findParamByPId',
                {crafworkId:row.crafworkId},
                data=>{
                    data.forEach(item=>{
                        delete item.createTime;
                        delete item.modifyTime
                    })
                    this.paramList=data;
                    this.paramSet.dialogVisible=true;
                },
                this
            )
        },
        loadNode(node,resolve,id){
            if(id==0){
                this.$http.post(
                    this.ports+'/prodCrafworkPlm/findAllProduct',
                    {},
                    data=>{
                        resolve(data)
                    },
                    this
                )
            }else{
                if(node.level<3){
                    this.$http.post(
                        this.ports+'/prodCrafworkPlm/findAllMidProductByProductNo',
                        {prodNo:node.data.prodNo},
                        data=>{
                            data.forEach(val=>{
                                val.isLeaf=true
                            })
                            resolve(data)
                        },
                        this
                    )
                }else{
                    resolve([])
                }
                
            }
        },
        nodeChange(node,data){
            this.selectData=data
            if(data.midProdNo){
                this.getList()
            }
        },
        getList(){
            this.$http.post(
                this.ports+'/prodCrafworkPlm/page',
                {midProdNo:this.selectData.midProdNo,prodNo:this.selectData.prodNo,pageNo:1,pageSize:100000},
                data=>{
                    this.tableData=data.items
                },
                this
            )
        },
        add(){
            this.toastConfig.title='新增产品工艺'
            this.form={
                crafworkId:'',
                crafworkDesc:'',
                quartersEmp:'',
                carfSeq:this.tableData.length <= 0 ? 1 : this.tableData[this.tableData.length-1].carfSeq+1,
                amount:0,
                id:0,
                unit:'',
                midProdNo:this.selectData.midProdNo,
                prodNo:this.selectData.prodNo
            }
            this.toastConfig.dialogVisible=true
        },
        edit(index,row){
                this.toastConfig.title='编辑产品工艺'
            this.form=row
            this.toastConfig.dialogVisible=true
        },
        del(index,row){
            this.$confirm('确认删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http.post(
                    this.ports+'/prodCrafworkPlm/remove',
                    {id:row.id,prodNo:this.selectData.prodNo,midProdNo:this.selectData.midProdNo},
                    data=>{
                        this.getList()
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    },
                    this
                )
            })
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(this.form.id==0){
                        this.$http.post(
                            this.ports+'/prodCrafworkPlm/addNew',
                            this.form,
                            data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.getList()
                            },
                            this
                        )
                    }else{
                        this.$http.post(
                            this.ports+'/prodCrafworkPlm/update',
                            this.form,
                            data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.getList()
                            },
                            this
                        )
                    }
                    
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        savaAgain(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post(
                        this.ports+'/productStorePlm/addProduct',
                        this.form,
                     data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=true;
                                this.getList()
                            },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    },
    mounted(){
        this.$http.post(
            this.ports+'/prodCrafworkPlm/optionCraf',
            {},
            data=>{
                this.crafworkList=data
            },
            this
        )
    }
}
</script>



