<template>
  <div class="checkCustomerData" style="background:#FFF;padding:10px;box-sizing:border-box;">
    <search :data="searchData" :config="searchConfig"    @search="search"></search>
      <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @upload="upload" dialogVisible:false></my-table>
      <my-toast :config="toastConfig" @submit="submit" @close="toastConfig.dialogVisible=false" >
        <el-form :model="form" ref="ruleForm" label-width="100px" style="margin-right:30px;"> 
          <el-row>
            <el-col :span='8'>
              <el-form-item label="订单编号">
                {{form.insideOrder}}
              </el-form-item>
            </el-col>
            <el-col :span='8'>
              <el-form-item label="客户信息">
                  {{form.customMess}}
              </el-form-item>
            </el-col>
            <el-col :span='8'>
              <el-form-item label="产品信息">
                {{form.prodMess}}
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span='8'>
              <el-form-item label="工艺执行人">
                {{form.doEmpName}}
              </el-form-item>
            </el-col>
             <el-col :span='8'>
              <el-form-item label="工艺名称">
                {{form.crafworkName}}
              </el-form-item>
            </el-col>
          </el-row>   
          <el-row>
            <el-col>
              <div class="imgList clearfix">
                <div  style="height:100px;max-width:100px;cursor:pointer;border:1px solid #666;"  @dblclick="centerDialogVisible = true;currentImg=item"  v-for="(item, index) in form.pictureUrls"  >
                    <img :src="item"  />
                </div>
            </div>
            </el-col>
            </el-row>
          </el-form>
        </my-toast>
        <el-dialog :visible.sync="centerDialogVisible" width="900px">
          <div >
            <div class="no-scroll" style="text-align: center;width: 800px;height: 500px;position: relative;overflow: auto;margin:0 auto;">
              <img :src="currentImg" style="position: absolute;left: 50%;top: 50%;transform: translate(-50%,-50%);max-width:100%"> 
            </div>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="centerDialogVisible = false">关 闭</el-button>
          </span>
        </el-dialog>
    </div>
</template>
<script>
import { mapState } from "vuex";
import upload from "../../components/page.vue";
export default {
  data() {
    return {
      currentImg: "",
      centerDialogVisible: false,
      pictureUrls: [],
      productList: [],
      deleteArr: [],
      selectData: [],
      tableData: [],
      tableConfig: {
        isChoose: false,
        isHigh: false,
        isSelection: false,
        align: "center",
        loadShow: false,
        isPage: true,
        pageNo: 1,
        pageSize: 20,
        total: 0,
        colConfig: [
          //表格字段配置
          {
            field: "insideOrder",
            label: "订单号",
            type: "text"
          },
          {
            field: "customMess",
            label: "客户信息",
            type: "text"
          },
          {
            field: "prodMess",
            label: "产品信息",
            type: "text"
          },
          {
            field: "crafworkName",
            label: "工艺名称",
            type: "text"
          },
          {
            field: "cusIsOk",
            label: "客户是否通过",
            type: "text"
          },
          {
            field: "checkEmpNo",
            label: "检查人",
            type: "text"
          },
          {
            field: "checkDate",
            label: "检查日期",
            type: "text"
          },
          {
            label: "操作",
            type: "button",
            btnData: [
              {
                name: "发起评审",
                method: "upload",
                showFunc: (scope, item) => {
                  return scope.row.cusIsOk == "未发起评审";
                }
              },
              {
                name: "查看详情",
                method: "upload",
                showFunc: (scope, item) => {
                  return scope.row.cusIsOk != "未发起评审";
                }
              }
            ]
          }
        ]
      },
      searchData: {},
      searchConfig: [
        [
          {
            label: "订单号",
            field: "insideOrder",
            filterable: true,
            type: "select",
            placeholder: "请选择订单编号",
            childrens: [],
            prop: {
              label: "insideOrder",
              value: "insideOrder"
            }
          },
          {
            label: "客户信息",
            field: "customMess",
            filterable: true,
            type: "select",
            placeholder: "请选择客户信息",
            childrens: [],
            prop: {
              label: "customMess",
              value: "customNo"
            }
          }
        ]
      ],
      toastConfig: {
        //弹框配置
        title: "发起评审",
        dialogVisible: false,
        width: "1000px",
        btnData: [
          {
            disabled: false,
            isShow: true,
            type: "primary",
            name: "发起评审", //按钮名
            method: "submit" //回调函数
          },
          {
            isShow: true,
            name: "关闭",
            method: "close"
          }
        ]
      },
      form: {
        fileList: []
      },
      jobs: []
      // 设置参数
    };
  },
  computed: {
    ...mapState(["ports"])
  },
  components: {
    upload
  },
  mounted() {
    this.getData(); //获得表格数据
    this.$http.post(
      //下拉框
      this.ports + "/deliveryProd/findCus",
      {},
      data => {
        this.searchConfig[0][1].childrens = data;
        this.productList = data;
      },
      this
    );
    this.$http.post(
      //下拉框
      this.ports + "/produceOrderAps/getOrder",
      {},
      data => {
        this.searchConfig[0][0].childrens = data;
        this.productList = data;
      },
      this
    );
  },
  methods: {
    getData() {
      /*这个方法是我自己定义的，作用：为了拿到后台数据*/

      var row = 10; /*这个是后台拿到的数据条数，可以根据length获取*/
      for (var i = 0; i < row; i++) {
        app.imgIstrue[i] = false; /*初始化为false*/
      }
    },
    imgScale(index) {
      /*这个方法是为了标识改变被点击图片的状态*/

      var isTrue = app.imgIstrue[index]; /*获取被点击元素当前的值 false或true*/
      app.imgIstrue.forEach(function(val, row) {
        app.imgIstrue.splice(
          row,
          1,
          false
        ); /*刷新数组，将数组所有值设置为false，即图片原始状态*/
      });
      app.imgIstrue.splice(index, 1, !isTrue);
    },
    upload(row, index) {
      console.log(row)
      this.$http.post(
        this.ports + "/pictMess/pictDetail",
        {
          insideOrder: row.insideOrder,
          prodNo: row.prodNo,
          crafworkId: row.crafworkId,
          customNo: row.customNo,
          getTime:row.getTime
        },
        data => {
          console.log(data, "======");
          this.form = {
            insideOrder: row.insideOrder,
            prodMess: row.prodMess,
            prodNo: row.prodNo,
            crafworkName: row.crafworkName,
            crafworkId: row.crafworkId,
            customMess: row.customMess,
            doEmpName: row.doEmpName,
            customNo: row.customNo,
            pictureUrls: data.pictureUrls,
            getTime:data.getTime,
            pictures: Object.assign([], data),
            fileList: Object.assign([], data)
          };
          if (row.cusIsOk == "未发起评审") {
            this.toastConfig.title = "发起评审";
            this.toastConfig.btnData[0].isShow = true;
          } else {
            this.toastConfig.title = "查看详情";
            this.toastConfig.btnData[0].isShow = false;
          }
          this.toastConfig.dialogVisible = true;
        },
        this
      );
    },
    submit() {
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http.post(
            this.ports + "/taskFinishedMes/tasks",
            {
              insideOrder: this.form.insideOrder,
              prodNo: this.form.prodNo,
              crafworkId: this.form.crafworkId,
              getTime:this.form.getTime
            },
            data => {
              this.$message({
                showClose: true,
                message: "提交成功",
                type: "success"
              });
              this.$refs.ruleForm.resetFields();
              this.toastConfig.dialogVisible = false;
              this.form, this.getData();
            },
            this
          );
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    close() {
      this.$http.post(
        this.ports + "/crafworkApprove/approveStatus",
        {
          id: this.form.id,
          cusIsOk: 3
        },
        data => {
          this.getData();
          this.toastConfig.dialogVisible = false;
        },
        this
      );
    },
    getData() {
      let param = JSON.parse(JSON.stringify(this.searchData));
      if (this.searchData.createDate) {
        param["startDate"] = param.createDate[0];
        param["endDate"] = param.createDate[1];
        delete param.createDate;
      }
      for (let i in param) {
        if (!param[i]) {
          delete param[i];
        }
      }
      this.$http.post(
        this.ports + "/taskFinishedMes/cusReview",
        {
          pageNo: this.tableConfig.pageNo,
          pageSize: this.tableConfig.pageSize,
          ...param
        },
        data => {
          this.tableData = data.items;
          this.tableConfig.total = data.numRows;
        },
        this
      );
    },
    pageChange() {
      this.getData();
    },
    search() {
      this.tableConfig.pageNo = 1;
      this.tableConfig.pageSize = 20;
      this.getData();
    }
  }
};
</script>
<style lang="less">
.checkCustomerData{
  .imgList{
      padding:0 20px;
      >div{
          width: 100px;
          height: 100px;
          position: relative;
          overflow: hidden;
          float: left;
          margin: 0px 10px;
          >img{
              position: absolute;
              left: 50%;
              top: 50%;
              width: 100%;
              transform: translate(-50%,-50%)
          }
      }
  }
}
  
</style>


