<template>
    <div class="technology">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <toolbar 
            :toolbarConfig="toolbarConfig"
            @handlerAdd="handlerAdd"
            @handlerEdit="handlerEdit"
            @handlerDel="handlerDel"
        ></toolbar>
        <my-table :data="tableData" :config="tableConfig" @select="handlerSelect" @pageChange="pageChange" @see="see" @del="del"></my-table>
        <my-toast :config="toastConfig" @handleOpen="handleOpen" @submit="handlerSubmit"  @cancel="toastConfig.dialogVisible=false">
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;"  v-if='toastConfig.dialogVisible == true' >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="订单来源" prop="orderSourceId" >
                            <el-select v-model="form.orderSourceId" :disabled="operateType=='edit'||operateType=='see'" placeholder="请选择订单来源" size="small" @change="orderSourceChange">
                              <el-option  label="销售订单" :value="1" ></el-option>
                              <el-option  label="销售预测" :value="2" ></el-option>
                            </el-select>
                            <!-- <el-input v-model="form.orderStatus" size="medium"></el-input> -->
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="内部订单号" prop="insideOrder">
                            <el-input v-model="form.insideOrder" :disabled="operateType=='edit'||operateType=='see'" size="small" placeholder="请输入内部订单号"></el-input>
                        </el-form-item>
                    </el-col>
                    <transition name="el-zoom-in-top">
                        <el-col :span="12" v-if="customShow">
                            <el-form-item label="客户信息" prop="customNo">
                                <el-select v-model="form.customNo" placeholder="请选择客户信息"  size="small" @change="handlerChange" :disabled="operateType=='see'">
                                    <el-option v-for="item in customs" :key="item.value" :label="item.customName" :value="item.customNo"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                     </transition>
                    <transition name="el-zoom-in-top">
                        <el-col :span="12" v-if="orderShow">
                            <el-form-item label="订单号" prop="orderNo" >
                                <el-select v-model="form.orderNo" placeholder="请选择订单号"  size="small" @change="handlerOrderChange" :disabled="operateType=='see'">
                                <el-option v-for="item in orders" :key="item.orderNo" :label="item.orderNo" :value="item.orderNo"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </transition>
                    
                    <transition name="el-zoom-in-top">
                        <el-col :span="12" v-if="otherShow">
                            <el-form-item label="下单日期" prop="voucherDate">
                                <el-date-picker  v-model="form.voucherDate" :disabled="form.orderNo!=''||operateType=='see'" size="small"  value-format="yyyy-MM-dd" placeholder="请输入下单日期" style="width:100%;"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </transition>
                    <el-col :span="4"></el-col>

                    <transition name="el-zoom-in-top">
                        <el-col :span="12" v-if="otherShow">
                            <el-form-item label="交图日期" prop="firstDate">
                                <el-date-picker  v-model="form.firstDate" :disabled="form.orderNo!=''||operateType=='see'" size="small" value-format="yyyy-MM-dd" placeholder="请输入交图日期" style="width:205px;"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </transition>
                    
                    <el-col :span="12">
                        <el-form-item label="生产部门" prop="orgId">
                            <el-select :disabled="operateType=='see'" v-model="form.orgId" placeholder="请选择生产部门" size="small">
                                <el-option v-for="item in orgList" :key="item.value" :label="item.orgName" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div class="myTable" style="width: 100%;padding:20px;box-sizing:border-box">
                <el-table
                    :data="detailList"
                    >
                    <el-table-column
                        prop="prodName"
                        label="产品信息">
                        <template slot-scope="scope">
                            <span v-if="form.orderSourceId==1||operateType=='see'">{{scope.row.prodName}}</span>
                            <el-select v-model="scope.row.prodNo" v-else size="medium" @change="handlerProdNoChange(scope.$index)">
                              <el-option v-for="item in productList" :label="item.prodName" :value="item.prodNo" :key="item.prodNo"></el-option>
                            </el-select>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="数量"
                        >
                            <template slot-scope="scope">
                                <span v-if="form.orderSourceId==1||operateType=='see'">{{scope.row.amount}}</span>
                                <el-input-number :min="0" v-model="scope.row.amount" v-else size="medium"></el-input-number>
                            </template>
                    </el-table-column>
                    <el-table-column
                        label="已交货数量"
                        v-if="operateType=='see'"
                        >
                            <template slot-scope="scope">
                                <span v-if="form.orderSourceId==1||operateType=='see'">{{scope.row.okAmount}}</span>
                                <el-input-number v-model="scope.row.okAmount" :min="1" v-else size="medium"></el-input-number>
                            </template>
                    </el-table-column>
                </el-table>
            </div>
        </my-toast>
    </div>         
</template>
<script>
import rule from '../../assets/js/rule.js'
import{mapState} from 'vuex'
export default {
    data(){
        return{
            operateType:"",
            // 产品列表
            productList:[],
            // 订单列表
            orders:[],
            // 客户信息
            customs:[],
            // 部门列表
            // 详情列表
            detailList:[],
            // 未改变时的数据
            initDetail:[],
            // 未改变的表单
            initForm:{},
            orgList:[],
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "内部订单号",
                        field: "insideOrder",
                        type: "input",
                        placeholder: "请输入姓名"
                    },
                    {
                        label: "客户信息",
                        field: "custom",
                        type: "input",
                        placeholder: "请输入客户信息"
                    },
                    {
                        label: "订单号",
                        field: "orderNo",
                        type: "input",
                        placeholder: "请输入订单号"
                    }
                ],
                [
                    {
                        label: "订单状态",
                        field: "orderStatus",
                        type: "select",
                        placeholder: "请输入姓名",
                        childrens:[{name:'审批中',value:'AS002'},{name:'已通过',value:'AS001'},{name:'已驳回',value:'AS003'}]
                    },
                    {
                        label: "下单日期",
                        field: "dateTime",
                        type: "daterange",
                        placeholder: "请输入手机号码"
                    }
                ]
            ],
            tableData:[1],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "orderSourceLabel",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "订单来源",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "内部订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "customName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "客户信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "orderNo",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "voucherDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "下单日期",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "firstDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "交图日期",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "orgName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "生产部门",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "orderStatusLabel",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "订单状态",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'订单明细',
                                method:'see'
                            },
                            {
                                name:'删除',
                                method:'del',
                                color:'#F6463B'
                            }
                        ]
                    }
                ]
            },
            toolbarConfig:{
                custom:[
                    {
                        icon: "plus",
                        type: "primary",
                        method: "handlerAdd",
                        name: "新增"
                    },
                    {
                        icon: "edit",
                        type: "",
                        method: "handlerEdit",
                        name: "编辑"
                    },
                    {
                        icon: "delete",
                        type: "",
                        method: "handlerDel",
                        name: "删除"
                    }
                ],
                btnStatus:[false,true,true]
            },
            toastConfig:{
                title:'新增订单',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'640px',//弹窗宽
                btnData:[{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'关闭',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                }]
            },
            form:{
                orderSourceId:'',
                customNo:'',
                orderNo:'',
                firstDate:'',
                orgId:'',
                voucherDate:'',
                insideOrder:''
            },
            rules:{
                orderSourceId:rule.require('number','请选择订单来源'),
                customNo:rule.require('string','请选择客户信息'),
                orderNo:rule.require('string','请选择订单号'),
                firstDate:rule.require('string','请选择交图时间'),
                voucherDate:rule.require('string','请选择下单时间'),
                insideOrder:rule.require('string','请输入内部订单'),
                orgId:rule.require('number','请选择生产部门'),
            },
            selectData:[],
        }
    },
    computed:{
        ...mapState(['ports']),
        customShow:function(){
            if(this.form.orderSourceId!=''){
                return true 
            }else{
                return false
            }
        },
        orderShow:function(){
            if(this.form.orderSourceId==1&&this.form.customNo!=''){
                return true
            }else{
                return false
            }
        },
        otherShow:function(){
            if(this.form.orderSourceId==1){
                if(this.form.customNo!=''&&this.form.orderNo!=''){
                    return true
                }else{
                    return false
                }
            }else{
                if(this.form.customNo!=''){
                    return true
                }else{
                    return false
                }
            }
        },
    },
    methods:{
        handlerDel(){
            var ary=[]
            for(let d of this.selectData){
                ary.push(d.voucherNo)
            }
            this.toDel(ary)
        },
        toDel(ary){
            this.$confirm('此操作将永久删除该订单, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http.post(
                    this.ports+'/produceOrderAps/delete',
                    {
                        voucherNos:ary.join(',')
                    },
                    data=>{
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        this.getList()
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        del(row){
            this.toDel([row.voucherNo])
        },
        see(row){
            this.toastConfig.btnData[1].isShow=false
            this.operateType='see'
            this.toastConfig.title='订单明细'
            this.$http.post(
                this.ports+'/produceOrderAps/getDetailByVoucherNo',
                {voucherNo:row.voucherNo},
                data=>{
                    console.log(data)
                    this.form={
                        orderSourceId:data.orderSourceId,
                        customNo:data.customNo,
                        orderNo:data.orderNo,
                        firstDate:data.firstDate,
                        orgId:data.orgId,
                        voucherDate:data.voucherDate,
                        insideOrder:data.insideOrder,
                        id:row.id,
                        voucherNo:row.voucherNo
                    }
                    // if(dat)
                    this.detailList=data.produceOrderDetailApsDtos
                    this.toastConfig.dialogVisible=true
                },
                this
            )
        },
        handlerEdit(){
            console.log(this.selectData[0])
            if(this.selectData[0].orderStatusLabel=="已通过"||this.selectData[0].orderStatusLabel=="审核中"){
                return this.$message(
                    {
                        type:'error',
                        message:'无法修改正在审核和审核完成的数据'
                    }
                )
            }
            this.toastConfig.btnData[1].isShow=true
            this.operateType='edit'
            this.toastConfig.title='编辑订单'
            this.$http.post(
                this.ports+'/produceOrderAps/getDetailByVoucherNo',
                {voucherNo:this.selectData[0].voucherNo},
                data=>{
                    console.log(data)
                    this.form={
                        orderSourceId:data.orderSourceId,
                        customNo:data.customNo,
                        orderNo:data.orderNo,
                        firstDate:data.firstDate,
                        orgId:data.orgId,
                        voucherDate:data.voucherDate,
                        insideOrder:data.insideOrder,
                        id:this.selectData[0].id,
                        voucherNo:this.selectData[0].voucherNo
                    }
                    // if(dat)
                    this.detailList=data.produceOrderDetailApsDtos
                    if(this.form.orderSourceId!=1){
                        this.detailList=data.produceOrderDetailApsDtos.concat([{prodNo:'',amount:0}]);
                    }
                    var index=this.customs.findIndex(item=>item.customNo==this.form.customNo)
                    var currentCustomId=this.customs[index].id
                    this.$http.post(
                        this.ports+'/orderDoc/listByCustomIdInCrm',
                        {customId:currentCustomId},
                        data=>{
                            this.orders=data
                            this.toastConfig.dialogVisible=true
                        },
                        this
                    )
                },
                this
            )
        },
        handlerSubmit(){
            var ary=JSON.parse(JSON.stringify(this.detailList))
            if(this.form.orderSourceId!=1){
                ary.splice(ary.length-1,1)
            }
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(this.operateType=='add'){
                        this.$http.post_noqs(
                            this.ports+'/produceOrderAps/save',
                            {
                                ...this.form,
                                produceOrderDetailApsDtos:ary
                            },
                            data=>{
                                this.toastConfig.dialogVisible=false
                                this.$message({
                                    type:'success',
                                    message:'新增成功'
                                })
                                this.getList()
                            },
                            this
                        )
                    }else{
                        this.$http.post_noqs(
                            this.ports+'/produceOrderAps/update',
                            {
                                ...this.form,
                                produceOrderDetailApsDtos:ary
                            },
                            data=>{
                                this.toastConfig.dialogVisible=false
                                this.getList()
                                this.$message({
                                    type:'success',
                                    message:'编辑成功'
                                })
                            },
                            this
                        )
                    }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            
        },
        handlerProdNoChange(index){
            if(index==this.detailList.length-1){
                this.detailList=this.detailList.concat([{prodNo:'',amount:0}])
            }
        },
        handleOpen(){
            this.initDetail=JSON.parse(JSON.stringify(this.detailList))
            this.initForm=JSON.parse(JSON.stringify(this.form))
        },
        handlerOrderChange(val){
            this.detailList=[]
            this.form.voucherDate=''
            this.form.firstDate=''
            if(val!=''){
                var index=this.orders.findIndex(data=>data.orderNo==val)
                this.$http.post(
                    this.ports+'/orderDoc/getByIdInCrm',
                    {id:this.orders[index].id},
                    data=>{
                        console.log(data.firstDate)
                        this.detailList=data.orderDetailCrmDtos
                        this.form.voucherDate=data.voucherDate
                        this.form.firstDate=data.firstDate
                    },
                    this
                )
            }
        },
        handlerChange(val){
            var index=this.customs.findIndex(item=>item.customNo==val)
            console.log(index,this.customs[index])
            var currentCustomId=this.customs[index].id
            this.$http.post(
                this.ports+'/orderDoc/listByCustomIdInCrm',
                {customId:currentCustomId},
                data=>{
                    if(this.initForm.customNo==val){
                        this.form.orderNo=this.initForm.orderNo;
                        this.detailList=this.initDetail;
                        this.form.voucherDate=this.initForm.voucherDate;
                        this.form.firstDate=this.initForm.firstDate;
                        if(val!=1){
                            this.detailList=this.initDetail.concat([{prodNo:'',amount:0}])
                        }else{
                            this.detailList=this.initDetail;
                        }
                    }else{
                        this.form.orderNo=''
                        this.detailList=[]
                        this.form.voucherDate=''
                        this.form.firstDate=''
                        if(val!=1){
                            this.detailList=[{prodNo:'',amount:0}];
                        }else{
                            this.detailList=[]
                        }
                    }
                    this.orders=data
                },
                this
            )
        },
        orderSourceChange(val){
            if(this.initForm.orderSourceId==val){
                this.form.customNo=this.initForm.customNo;
                this.form.orderNo=this.initForm.orderNo;
                this.detailList=this.initDetail;
                this.form.voucherDate=this.initForm.voucherDate;
                this.form.firstDate=this.initForm.firstDate;
                if(val!=1){
                     this.detailList=this.initDetail.concat([{prodNo:'',amount:0}])
                }else{
                    this.detailList=this.initDetail;
                }
            }else{
                this.form.orderNo='';
                this.form.voucherDate='';
                this.form.firstDate='';
                this.form.customNo='';
                if(val!=1){
                     this.detailList=[{prodNo:'',amount:0}];
                }else{
                    this.detailList=[]
                }
            }
        },
        handlerAdd(){
            this.toastConfig.btnData[1].isShow=true
            this.operateType='add'
            this.toastConfig.title='新增订单'
            this.toastConfig.dialogVisible=true;
            this.form={
                orderSourceId:'',
                customNo:'',
                orderNo:'',
                firstDate:'',
                orgId:'',
                voucherDate:''
            }
            this.detailList=[]
        },
        search(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        pageChange(){
            this.getList()
        },
        handlerSelect(data){
            this.selectData=data
            if(data.length==0){
                this.toolbarConfig.btnStatus=[false,true,true]
            }else if(data.length==1){
                this.toolbarConfig.btnStatus=[false,false,false]
            }else{
                this.toolbarConfig.btnStatus=[false,true,false]
            }
        },
        getList(){
            var param=Object.assign({},this.searchData)
            if(param.dateTime){
                param.voucherDateFrom=param.dateTime[0];
                param.voucherDateTo=param.dateTime[1]
            }
            delete param.dateTime
            this.$http.post(
                this.ports+'/produceOrderAps/list',
                {
                    ...param,
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize
                },
                data=>{
                    this.tableData=data.items
                    this.tableConfig.total=data.numRows
                },
                this
            )
        }
    },
    mounted(){
        this.getList()
        this.$http.post(
            this.ports+'/custom/listInCrm',
            {},
            data=>{
                this.customs=data
            },
            this
        )
        this.$http.post(
            this.ports+'/casOrg/listInHcm',
            {},
            data=>{ 
                console.log(data)
                this.orgList=data
            },
            this
        )
        this.$http.post(
            this.ports+'/productStorePlm/listForQueryCriteria',
            {},
            data=>{
                this.productList=data
            },
            this
        )
    }
}
</script>
<style lang="less">
    .technology{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




