// export default routers
import Vue from 'vue'
import Router from 'vue-router'
//主路由
import index from '../page/index.vue'
import technology from '../page/technology/index.vue' 
import product from '../page/product/index'
import semiManfactures from '../page/semiManufactures/index'
import productProcess from '../page/productProcess/index'
import internalOrder from '../page/internalOrder/index'
import tasking from '../page/tasking/index'
import receive from '../page/receive/index'
import jobAssignment from '../page/jobAssignment/index'
import approve from '../page/approve/index.vue'
import customerReview from '../page/review/index'
import customerProcessReview from '../page/processReview/index'
import customerUploadData from '../page/customerUploadData/index'
import taskingRecord from '../page/taskingRecord/index'
import jobAssignmentHistory from '../page/jobAssignmentHistory/index'
import electronicHairMap from '../page/electronicHairMap/index.vue'
import seeUpload from '../page/checkCustomerData/index.vue'
Vue.use(Router)
export default new Router({
  routes: [{
      path: "/index",
      name: 'index',
      component: index,
      children: [
        {
          path:'/jobAssignmentHistory',
          name: 'jobAssignmentHistory',
          component:jobAssignmentHistory
        },
        {
          path:'/taskingRecord',
          name: 'taskingRecord',
          component:taskingRecord
        },
        {
          path:'/customerUploadData',
          name: 'customerUploadData',
          component:customerUploadData
        },
        {
          path:'/customerProcessReview',
          name: 'customerProcessReview',
          component:customerProcessReview
        },
        {
          path:'/customerReview',
          name: 'customerReview',
          component:customerReview
        },
        {
          path:'/approve',
          name: 'approve',
          component:approve
        },
        {
          path:'/receive',
          name: 'receive',
          component:receive
        },
        {
          path:'/jobAssignment',
          name: 'jobAssignment',
          component:jobAssignment
        },
        {
          path:'/tasking',
          name: 'tasking',
          component:tasking
        },
        {
          path:'/technology',
          name: 'technology',
          component:technology
        },
        {
          path:'/product',
          name: 'product',
          component:product
        },
        {
          path:'/semi-manufactures',
          name: 'semiManfactures',
          component:semiManfactures
        },
        {
          path:'/productProcess',
          name: 'productProcess',
          component:productProcess
        },
        {
          path:'/internalOrder',
          name: 'internalOrder',
          component:internalOrder
        },
        {
          path:'/electronicHairMap',
          name: 'electronicHairMap',
          component:electronicHairMap
        },{
          path:'/seeUpload',
          name:'seeUpload',
          component:seeUpload
        }
      ]
    }
  ]
})



