<template>
    <div class="semiManufactures">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <toolbar 
            :toolbarConfig="toolbarConfig"
            @handlerAdd="handlerAdd"
            @handlerEdit="handlerEdit"
            @handlerDel="handlerDel"
        ></toolbar>
        <my-table :data="tableData" :config="tableConfig"  @pageChange="pageChange" @select="select" @see="see"></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()" @savaAgain="savaAgain">
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;"  v-if='toastConfig.dialogVisible == true' >
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="产品信息" prop="message">
                            <!-- <el-input v-model="form.prodNo" size="medium"></el-input> -->
                            <el-select v-model="form.message" placeholder="请选择产品信息" size="medium" style="width:100%" @change="handlerChange">
                                <el-option v-for="(prodNo,index) in prodNos" :key="index" :label="prodNo.prodName+'-'+prodNo.prodNo" :value="prodNo.prodNo+','+prodNo.prodName"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="半产品编码" prop="midProdNo">
                            <el-input :disabled="form.id!=0" v-model="form.midProdNo" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="计量单位" prop="unit">
                            <el-input v-model="form.unit" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                    <!-- <el-col :span="12">
                        <el-form-item label="所属工艺" prop="crafId">
                            <el-select v-model="form.crafId" placeholder="请选择工艺岗位" size="medium" style="width:100%" multiple >
                                <el-option v-for="(job,index) in productList" :key="index" :label="job.crafworkName" :value="job.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col> -->
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="参数" prop="param">
                            <el-input v-model="form.param" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="型号" prop="modelDesc">
                            <el-input v-model="form.modelDesc" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="规格" prop="norms">
                            <el-input v-model="form.norms" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="备注" prop="remark">
                    <el-input v-model="form.remark" type="textarea"></el-input>
                </el-form-item>
            </el-form>
        </my-toast>
        <my-toast :config="detailConfig" @cancel="detailConfig.dialogVisible=false">
            <el-form :model="viewForm"  ref="ruleForm" label-width="100px" style="margin-right:30px;">
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="产品信息:" >
                            {{viewForm.prodName+'-'+viewForm.prodNo}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="半产品编码:" >
                            {{viewForm.midProdNo}}
                        </el-form-item>
                    </el-col>
                    <!-- <el-col :span="12">
                        <el-form-item label="所属工艺:" >
                            {{viewForm.crafworkName}}
                        </el-form-item>
                    </el-col> -->
                    <el-col :span="12">
                        <el-form-item label="计量单位:" >
                            {{viewForm.unit}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    
                    <el-col :span="12">
                        <el-form-item label="参数:" >
                            {{viewForm.param}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="型号:">
                            {{viewForm.modelDesc}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="规格:">
                            {{viewForm.norms}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="备注:">
                    {{viewForm.remark}}
                </el-form-item>
            </el-form>
        </my-toast>
    </div>
</template>
<style lang="less">
    .semiManufactures{
        padding:20px;
        background:#fff;
    }
</style>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            rules:{
                message:rule.require('string','请选择产品信息'),
                midProdNo:rule.require('string','半产品编码不能为空'),
                // crafId:rule.require(),
                unit:rule.require('string','计量单位不能为空')
            },
            // 产品信息列表
            prodNos:[],
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "半成品编码",
                        field: "midProdNo",
                        type: "input",
                        placeholder: "请输入姓名"
                    },
                    {
                        label: "产品信息",
                        field: "prodNo",
                        type: "input",
                        placeholder: "请输入手机号码"
                    },

                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                currentSelectArr: [],
                colConfig: [
                    {
                        fixed: false,
                        field: "midProdNo",
                        isSort: false,
                        isResize: true,
                        label: "半成品编码",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        isSort: false,
                        isResize: true,
                        label: "产品名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodNo",
                        isSort: false,
                        isResize: true,
                        label: "产品编码",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "param",
                        isSort: false,
                        isResize: true,
                        label: "参数",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "norms",
                        isSort: false,
                        isResize: true,
                        label: "规格",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "modelDesc",
                        isSort: false,
                        isResize: true,
                        label: "型号",
                        align: "center",
                        type: "text"
                    },
                    // {
                    //     fixed: false,
                    //     field: "crafworkName",
                    //     isSort: false,
                    //     isResize: true,
                    //     label: "所属工艺",
                    //     align: "center",
                    //     type: "text"
                    // },
                    {
                        fixed: false,
                        width: "140px",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'查看详情',
                                method:'see'
                            }
                        ]
                    }
                ]
            },
            toolbarConfig:{
                custom:[
                    {
                        icon: "plus",
                        type: "primary",
                        method: "handlerAdd",
                        name: "新增"
                    },
                    {
                        icon: "edit",
                        type: "",
                        method: "handlerEdit",
                        name: "编辑"
                    },
                    {
                        icon: "delete",
                        type: "",
                        method: "handlerDel",
                        name: "删除"
                    }
                ],
                btnStatus:[false,true,true]
            },
            toastConfig:{
                title:'新增半成品',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    name:'确定并继续',
                    type:'primary',
                    method:'savaAgain',
                    bgColor:'dialog-deep-blue'
                }]
            },
            // 新增表单
            form:{
                midProdNo:'',
                prodName:'',
                prodNo:'',
                param:'',
                norms:'',
                modelDesc:'',
                // crafId:[],
                unit:'',
                remark:''
            },
            // 工艺列表
            productList:[],
            viewForm:{},
            detailConfig:{
                title:'查看详情',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'关闭窗口',//按钮名
                    method:'cancel',//回调函数
                }]
            },
        }
    },
    computed:{
        ...mapState(['ports','prodTypeList'])
    },
    methods:{
        handlerChange(val){
            if(val){
                var ary=val.split(',')
                this.form.prodName=ary[1]
                this.form.prodNo=ary[0]
            }
        },
        handlerAdd(){
            this.toastConfig.btnData[2].isShow=true
            this.toastConfig.title='新增半成品'
            this.form={
                midProdNo:'',
                prodName:'',
                prodNo:'',
                param:'',
                norms:'',
                modelDesc:'',
                unit:'',
                remark:'',
                message:'',
                id:0
            },
            this.toastConfig.dialogVisible=true
        },
        handlerEdit(){
            this.toastConfig.title='编辑半成品'
            this.form=Object.assign({},this.selectData[0])
            // this.form.message=this.form.prodName+this.form.prodNo
            this.$set(this.form,'message',this.selectData[0].prodNo+','+this.selectData[0].prodName)
            this.toastConfig.btnData[2].isShow=false
            this.toastConfig.dialogVisible=true
        },
        handlerDel(){
            this.$confirm('确认删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var ids=[]
                for(let d of this.selectData){
                    ids.push(d.id)
                }
                this.$http.post(
                    this.ports+'/midProduct/remove',
                    {ids:ids.join(',')},
                    data=>{
                        this.getList()
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(this.form.id==0){
                        this.$http.post(
                            this.ports+'/midProduct/add',
                            this.form,
                            data=>{
                                this.$message({
                                    type:'success',
                                    message: '新增成功!'
                                })
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.getList()
                            },
                            this
                        )
                    }else{
                        this.$http.post(
                            this.ports+'/midProduct/update',
                            this.form,
                            data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                 this.form={
                                // crafworkName:'',
                                    quartersEmp:'',
                                    actHours:'',
                                    unit:''
                                }
                                this.$message({
                                    type:'success',
                                    message: '编辑成功!'
                                })
                                this.getList()
                            },
                            this
                        )
                    }
                    
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        savaAgain(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post(
                        this.ports+'/midProduct/add',
                        this.form,
                        data=>{
                                this.$message({
                                    type:'success',
                                    message: '新增成功!'
                                })
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=true;
                                this.getList()
                            },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        select(data){
            this.selectData=data
            if(data.length==1){
                this.toolbarConfig.btnStatus=[false,false,false]
            }else if(data.length==0){
                this.toolbarConfig.btnStatus=[false,true,true]
            }else{
                this.toolbarConfig.btnStatus=[false,true,false]
            }
        },
        getList(){
            this.$http.post(
                this.ports+'/midProduct/page',
                {
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize,
                    ...this.searchData
                },
                data=>{
                    console.log(this.searchData,'11111111')
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        pageChange(){
            this.getList()
        },
        search(){
            this.tableConfig.pageNo=1;
            this.tableConfig.pageSize=20;
            this.getList()
        },
        see(data){
            this.viewForm=Object.assign({},data)
            this.detailConfig.dialogVisible=true
        }
    },
    mounted(){
        this.getList()
        this.$http.post(
            this.ports+'/midProduct/optionProductStorePlm',
            {},
            data=>{
                this.prodNos=data
            },
            this
        )
        this.$http.post(
            this.ports+'/midProduct/optionCrafworkStructPlm',
            {},
            data=>{
                console.log(data,'++++++')
                // this.searchConfig[0][2].childrens=data
                this.productList=data
            },
            this
        )
    }
}
</script>





