<template>
    <div class="processReview">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <my-table 
        :data="tableData" 
        :config="tableConfig"
        @pageChange="pageChange"
        @review="review"
        ></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="cancel" >
            <el-form :model="form"  ref="ruleForm" label-width="100px" style="margin-right:30px;">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="订单号:">
                            {{form.insideOrder}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="产品信息:">
                            {{form.prodName}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="工艺信息:">
                            {{form.crafworkName}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="工艺执行人:">
                            {{form.empName}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="图片提交人:">
                            {{form.empName}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                  <el-col :span="16">
                      <el-form-item label="描述">
                          <el-input v-model="form.desc" type="textarea" placeholder="请输入..." :rows="3"></el-input>
                      </el-form-item>
                  </el-col>
                </el-row>
            </el-form>
            <div class="imgList clearfix">
                <div v-for="item in form.imgList" @dblclick="centerDialogVisible = true;currentImg=item.linkPath">
                    <img :src="item.linkPath"  />
                </div>
            </div>
        </my-toast>
        <el-dialog :visible.sync="centerDialogVisible" width="900px">
          <div >
            <div class="no-scroll" style="text-align: center;width: 800px;height: 500px;position: relative;overflow: auto;margin:0 auto;">
              <img :src="currentImg" style="position: absolute;left: 50%;top: 50%;transform: translate(-50%,-50%);max-width:100%"> 
            </div>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="centerDialogVisible = false">关 闭</el-button>
          </span>
        </el-dialog>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            centerDialogVisible:false,
            currentImg:'',
            // 要删除的参数
            deleteArr:[],
            // 当前表格选中项
            selectData:[],
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "订单号",
                        field: "insideOrder",
                        type: "select",
                        childrens:[],
                        prop:{
                            label:'insideOrder',
                            value:'insideOrder'
                        }
                    },
                    {
                        label: "产品信息",
                        field: "prodNo",
                        type: "select",
                        childrens:[],
                        prop:{
                            label:'prodName',
                            value:'prodNo'
                        }
                    }
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: false,
                align: "center",
                loadShow:false,
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "empName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺执行人",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "cusIsOk",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "客户是否通过",
                        align: "center",
                        type: "text"
                    },
                    
                    {
                        field: "desc",
                        label: "描述",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "200px",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'工艺评审',
                                method:'review'
                            }
                        ]
                    }
                ]
            },
            toastConfig:{
                title:'工艺评审',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'752px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'不通过',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'通过',//按钮名
                    method:'submit',//回调函数
                }]
            },
            form:{},
            jobs:[],
            // 设置参数
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        // 评审
        review(row,index){
            if(row.cusIsOk=='待评审'){
                this.form={...row}
                this.$http.post(
                    this.ports+'/crafworkApprove/approve',
                    {crafworkId:row.crafworkId,prodNo:row.prodNo,insideOrder:row.insideOrder,getTime:row.getTime},
                    data=>{
                        // this.paramForm.id=row.id;
                        this.toastConfig.dialogVisible=true
                        // this.form.imgList=data
                        this.$set(this.form,'imgList',data)
                    },
                    this
                )
            }else{
                this.$message.error('已完成审核请勿重复操作')
            }
            
            // this.paramSet.dialogVisible=true
        },
        submit(){
            this.$http.post(
                this.ports+'/crafworkApprove/approveStatus',
                {
                    id:this.form.id,
                    cusIsOk:2,
                    desc:this.form.desc
                },
                data=>{
                    this.getList()
                    this.toastConfig.dialogVisible=false
                },
                this
            )
        },
        cancel(){
            this.$http.post(
                this.ports+'/crafworkApprove/approveStatus',
                {
                    id:this.form.id,
                    cusIsOk:3
                },
                data=>{
                    this.getList()
                    this.toastConfig.dialogVisible=false
                },
                this
            )
        },
        handlerSearch(){
            this.tableConfig.pageNo=1;
            this.tableConfig.pageSize=20;
            this.getList()
        },
        getList(){
            this.$http.post(
                this.ports+'/crafworkApprove/page',
                {
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        pageChange(){
            this.getList()
        }
    },
    mounted(){
        this.getList()
        this.$http.post(
            this.ports+'/crafworkApprove/optionOrderNo',
            {},
            data=>{
                this.searchConfig[0][0].childrens=data
            },
            this
        )
        this.$http.post(
            this.ports+'/crafworkApprove/optionProd',
            {},
            data=>{
                this.searchConfig[0][1].childrens=data
            },
            this
        )
    }
}
</script>
<style lang="less">
    .processReview{
       padding:20px;
       background:#fff;
        .imgList{
            padding:0 20px;
            >div{
                width: 100px;
                height: 100px;
                position: relative;
                overflow: hidden;
                float: left;
                margin: 0px 10px;
                >img{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 100%;
                    transform: translate(-50%,-50%)
                }
            }
        }
    }
</style>




