<template>
    <div class="uploadPage clearfix">
        <div id="filePicker">
            <i class="el-icon-plus"></i>
        </div>
        <div class="file" v-show="isUploading">
            <div class="cover"></div>
            <el-progress type="circle" :percentage="percentage" :width="50" :status="myStatus" :stroke-width="3"></el-progress>
        </div>
        <div v-for="item in form.fileList" class="file">
            <img :src="item" alt="">
        </div>
        <vue-upload
            ref="uploader"
            uploadButton="#filePicker"
            @fileChange="fileChange"
            @progress="onProgress"
            @success="onSuccess"
            @uploadError="uploadError"
            :formData="{
                appSrc:'mes'
            }"
        ></vue-upload>
    </div>
</template>

<script>
    import vueUpload from './upload';
    // import api from '@/assets/js/api';
    export default {
        data() {
            return {
                percentage:0,
                myStatus:'',
                isUploading:false
            }
        },
        props:['form'],
        computed: {
            uploader() {
                return this.$refs.uploader;
            }
        },
        methods: {
            fileChange(file) {
                if (!file.size) return;
                this.isUploading=true
                this.percentage=0
                this.myStatus=''
            },
            onProgress(file, percent) {
                this.percentage=parseInt((percent * 100).toFixed(0))
            },
            uploadError(err){
                this.myStatus='exception'
                this.isUploading=false
                this.$message.error(err)
            },
            onSuccess (file, response) {
                console.log(file)
                if (response.result) {
                    this.myStatus='success'
                    setTimeout(()=>{
                        this.isUploading=false
                        this.form.fileList.unshift(response.model)
                        this.form.pictures.push(response.model)
                    },1000)
                }else{
                    this.myStatus='exception'
                    this.$alert(response.message,'系统提示')
                    setTimeout(()=>{
                        this.isUploading=false
                    },3000)
                }
            },
            resume(file) {
                this.uploader.upload(file);
            },
            stop(file) {
                this.uploader.stop(file);
            },
            remove(file) {
                // 取消并中断文件上传
                this.uploader.cancelFile(file);
                // 在队列中移除文件
                this.uploader.removeFile(file, true);
                // 在ui上移除
                let index = this.form.fileList.findIndex(ele => ele.id === file.id);
                this.form.fileList.splice(index, 1);
            },
            fileSize(size) {
                return WebUploader.Base.formatSize(size);
            },
            fileCategory(ext) {
                let type = '';
                const typeMap = {
                    image: ['gif', 'jpg', 'jpeg', 'png', 'bmp', 'webp'],
                    video: ['mp4', 'm3u8', 'rmvb', 'avi', 'swf', '3gp', 'mkv', 'flv'],
                    text: ['doc', 'txt', 'docx', 'pages', 'epub', 'pdf', 'numbers', 'csv', 'xls', 'xlsx', 'keynote', 'ppt', 'pptx']
                };
                Object.keys(typeMap).forEach((_type) => {
                    const extensions = typeMap[_type];
                    if (extensions.indexOf(ext) > -1) {
                        type = _type
                    }
                });
                return type
            },
        },
        watch: {},
        components: {
            vueUpload
        }
    }
</script>

<style lang="less">
    .uploadPage{
        #filePicker{
            float: left;
            margin-top: 10px;
            margin-right: 10px;
            height: 100px;
        }
        .file{
            float: left;
            margin-top: 10px;
            width: 100px;
            height: 100px;
            line-height: 100px;
            text-align: center;
            margin-right: 10px;
            border:1px dashed #999;
            position: relative;
            img{
                width: 100%;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
            }
            .cover{
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                background: #000;
                opacity: .6;
            }
            .el-progress{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
                .el-progress__text{
                    color: #fff;
                }
            }
        }
    }
</style>


