<template>
    <div class="receive">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @receive="receive" ></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()"> 
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="110px" style="margin:0 20px;">
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="内部订单号" >
                            <span>{{form.insideOrder}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="产品信息" >
                            <span>{{form.prodName}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                      <el-form-item label="数量" >
                            <span>{{form.amount}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                            <el-form-item label="工艺名称" >
                                <span>{{form.crafworkName}}</span>
                            </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="计划开始时间" prop="planDate">
                            <el-date-picker
                                v-model="form.planDate"
                                type="datetime"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                 size="small"
                                 style="width:100%"
                                placeholder="选择计划开始时间">
                            </el-date-picker>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="计划工时" prop="planHours">
                            <el-input-number :min="0" v-model="form.planHours" :controls="false" size="small"></el-input-number>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item label="实际开始时间" prop="actDate">
                    <el-date-picker
                        v-model="form.actDate"
                        type="datetime"
                        value-format="yyyy-MM-dd HH:mm:ss"
                            size="small"
                            style="width:100%"
                        placeholder="选择计划开始时间">
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            //工艺执行人列表
            userList:[],
            selectData:[],
            rules:{
                price:rule.require('number','工艺名称不能为空'),
                doEmpNo:rule.require('string','请选择工艺执行人'),
                planDate:rule.require('string','请选择计划开始时间'),
                planHours:rule.require('number','计划工时不能为空'),
                actDate:rule.require('string','请选择实际开始时间'),
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "内部订单号",
                        field: "insideOrder",
                        type: "input",
                        placeholder: "请输入内部订单号"
                    },
                    {
                        label: "产品信息",
                        field: "prodName",
                        type: "input",
                        placeholder: "请输入产品信息"
                    },
                    {
                        label: "任务状态",
                        field: "status",
                        type: "select",
                        placeholder: "请输入姓名",
                        childrens:[{name:'待派工',value:'0'},{name:'处理中',value:'1'},{name:'已完成',value:'2'},{name:'审核未通过',value:'3'}]
                    },
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "内部订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "price",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺工价",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "doEmpName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺执行人",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "amount",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "数量",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计划开始时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "actDate",
                        width: "120",
                        isSort: false,
                        isResize: true,
                        label: "实际开始时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "getTime",
                        isSort: false,
                        isResize: true,
                        label: "领派工时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkStandHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "标准工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计划工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "statusName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "任务状态",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'任务领取',
                                method:'receive'
                            }
                        ]
                    }
                ]
            },
            toastConfig:{
                title:'任务领取',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'560px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                }]
            },
            form:{

            },
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        getList(){
            this.$http.post(
                this.ports+'/taskPondMes/listForDrawTask',
                {
                    pageSize:this.tableConfig.pageSize,
                    pageNo:this.tableConfig.pageNo,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        handlerSearch(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post_noqs(
                        this.ports+'/taskPondMes/drawTask',
                        this.form,
                        data=>{
                            this.$refs.ruleForm.resetFields()
                            this.dialogVisible=true
                            this.$message({
                                type: 'success',
                                message:'任务领取成功'
                            })
                            this.toastConfig.dialogVisible=false
                            this.getList()
                        },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        receive(val){
            if(val.status!=0){
                return this.$message.error('任务不可领取，请确认任务是否已被领取或已经分配')
            }
            this.form=Object.assign({},val)
             this.toastConfig.dialogVisible=true
            // this.$http.post(
            //     this.ports+'/taskPondMes/getById',
            //     {id:val.id},
            //     data=>{
            //         console.log(data)
            //         this.userList=data
                   
            //     },
            //     this
            // )
            
        },
        pageChange(){
            console.log('111')
            this.getList()
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    .receive{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




