<template>
    <div class="technology">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <my-table :data="tableData" :config="tableConfig"  @pageChange="pageChange" @see="see" @approve="approve"></my-table>
        <my-toast :config="toastConfig" @unSubmit="unSubmit"  @submit="handlerSubmit" @cancel="toastConfig.dialogVisible=false">
            <el-form :model="form" ref="ruleForm" label-width="100px" style="margin-right:30px;">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="订单来源" prop="orderSourceId" >
                            <el-select v-model="form.orderSourceId" :disabled="operateType=='edit'||operateType=='see'" placeholder="请选择订单来源" size="small" >
                              <el-option  label="销售订单" :value="1" ></el-option>
                              <el-option  label="销售预测" :value="2" ></el-option>
                            </el-select>
                            <!-- <el-input v-model="form.orderStatus" size="medium"></el-input> -->
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="内部订单号" prop="insideOrder">
                            <el-input v-model="form.insideOrder" :disabled="operateType=='edit'||operateType=='see'" size="small" placeholder="请输入内部订单号"></el-input>
                        </el-form-item>
                    </el-col>
                        <el-col :span="12" v-if="customShow">
                            <el-form-item label="客户信息" prop="customNo">
                                <el-select v-model="form.customNo" placeholder="请选择客户信息"  size="small"  :disabled="operateType=='see'">
                                    <el-option v-for="item in customs" :key="item.value" :label="item.customName" :value="item.customNo"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" v-if="orderShow">
                            <el-form-item label="订单号" prop="orderNo" >
                                <el-select v-model="form.orderNo" placeholder="请选择订单号"  size="small"  :disabled="operateType=='see'">
                                	<el-option v-for="item in orders" :key="item.orderNo" :label="item.orderNo" :value="item.orderNo"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    
                        <el-col :span="12" v-if="otherShow">
                            <el-form-item label="下单日期" prop="voucherDate">
                                <el-date-picker style="width:101%" v-model="form.voucherDate" :disabled="form.orderNo!=''||operateType=='see'" size="small"  value-format="yyyy-MM-dd" placeholder="请输入下单日期"></el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" v-if="otherShow">
                            <el-form-item label="交图日期" prop="firstDate">
                                <el-date-picker  style="width:205px;" v-model="form.firstDate" :disabled="form.orderNo!=''||operateType=='see'" size="small" value-format="yyyy-MM-dd" placeholder="请输入交图日期"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    <el-col :span="12">
                        <el-form-item label="生产部门" prop="orgId">
                            <el-select :disabled="operateType=='see'" v-model="form.orgId" placeholder="请选择生产部门" size="small">
                                <el-option v-for="item in orgList" :key="item.value" :label="item.orgName" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div class="myTable" style="width: 100%;padding:20px;box-sizing:border-box">
                <el-table
                    :data="detailList"
                    >
                    <el-table-column
                        prop="prodName"
                        label="产品信息">
                        <template slot-scope="scope">
                            <span >{{scope.row.prodName}}</span>
                             <!--<el-select v-model="scope.row.prodNo" v-else size="medium" @change="handlerProdNoChange(scope.$index)">
                              <el-option v-for="item in productList" :label="item.prodName" :value="item.prodNo" :key="item.prodNo"></el-option>
                            </el-select> -->
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="数量"
                        >
                            <template slot-scope="scope">
                                <!-- v-if="form.orderSourceId==1||operateType=='see'" -->
                                <span >{{scope.row.amount}}</span>
                                <!-- <el-input-number v-model="scope.row.amount" v-else size="medium"></el-input-number> -->
                            </template>
                    </el-table-column>
                    <el-table-column
                        label="已交货数量"
                        v-if="operateType=='see'"
                        >
                            <template slot-scope="scope">
                                <span >{{scope.row.okAmount}}</span>
                            </template>
                    </el-table-column>
                </el-table>
            </div>
        </my-toast>
    </div>         
</template>
<script>
import rule from '../../assets/js/rule.js'
import{mapState} from 'vuex'
export default {
    data(){
        return{
            operateType:"see",
            // 产品列表
            productList:[],
            // 订单列表
            orders:[],
            // 客户信息
            customs:[],
            // 部门列表
            // 详情列表
            detailList:[],
            // 未改变时的数据
            initDetail:[],
            // 未改变的表单
            initForm:{},
            orgList:[],
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "内部订单号",
                        field: "insideOrder",
                        type: "input",
                        placeholder: "请输入姓名"
                    },
                    {
                        label: "客户信息",
                        field: "custom",
                        type: "input",
                        placeholder: "请输入手机号码"
                    },
                    {
                        label: "订单号",
                        field: "orderNo",
                        type: "input",
                        placeholder: "请输入手机号码"
                    }
                ],
                [
                    {
                        label: "订单状态",
                        field: "orderStatus",
                        type: "select",
                        placeholder: "请输入姓名",
                        childrens:[{name:'审批中',value:'AS002'},{name:'已通过',value:'AS001'},{name:'已驳回',value:'AS003'}]
                    },
                    {
                        label: "下单日期",
                        field: "dateTime",
                        type: "daterange",
                        placeholder: "请输入手机号码"
                    }
                ]
            ],
            tableData:[1],
            tableConfig:{
                isHigh: false,
                isSelection: false,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "orderSourceLabel",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "订单来源",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "内部订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "customName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "客户信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "orderNo",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "voucherDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "下单日期",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "firstDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "交图日期",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "orgName",//
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "生产部门",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "orderStatusLabel",
                        width: "",
                        isSort: false,
                        isResize: true,

                        label: "订单状态",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'订单明细',
                                method:'see'
                            },
                            {
                                name:'审批',
                                method:'approve',
                                color:'#F6463B'
                            }
                        ]
                    }
                ]
            },
            toastConfig:{
                title:'新增订单',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'640px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'不通过',//按钮名
                    method:'unSubmit',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'通过',//按钮名
                    method:'submit',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'关闭',//按钮名
                    method:'cancel',//回调函数
                }]
            },
            form:{
                orderSourceId:'',
                customNo:'',
                orderNo:'',
                firstDate:'',
                orgId:'',
                voucherDate:'',
                insideOrder:''
            }
        }
    },
    computed:{
        ...mapState(['ports']),
        customShow:function(){
            if(this.form.orderSourceId!=''){
                return true 
            }else{
                return false
            }
        },
        orderShow:function(){
            if(this.form.orderSourceId==1&&this.form.customNo!=''){
                return true
            }else{
                return false
            }
        },
        otherShow:function(){
            if(this.form.orderSourceId==1){
                if(this.form.customNo!=''&&this.form.orderNo!=''){
                    return true
                }else{
                    return false
                }
            }else{
                if(this.form.customNo!=''){
                    return true
                }else{
                    return false
                }
            }
        },
    },
    methods:{
        see(row){
            this.operateType='see'
            this.toastConfig.title='订单明细'
            this.toastConfig.btnData[0].isShow=false
            this.toastConfig.btnData[1].isShow=false
            this.toastConfig.btnData[2].isShow=true
            this.$http.post(
                this.ports+'/produceOrderAps/getDetailByVoucherNo',
                {voucherNo:row.voucherNo},
                data=>{
                    console.log(data)
                    this.form={
                        orderSourceId:data.orderSourceId,
                        customNo:data.customNo,
                        orderNo:data.orderNo,
                        firstDate:data.firstDate,
                        orgId:data.orgId,
                        voucherDate:data.voucherDate,
                        insideOrder:data.insideOrder,
                        id:row.id,
                        voucherNo:row.voucherNo
                    }
                    // if(dat)
                    this.detailList=data.produceOrderDetailApsDtos
                    this.toastConfig.dialogVisible=true
                },
                this
            )
        },
        approve(row){
            this.operateType='see'
            this.toastConfig.title='订单明细'
            this.toastConfig.btnData[0].isShow=true
            this.toastConfig.btnData[1].isShow=true
            this.toastConfig.btnData[2].isShow=false
            if(row.orderStatusLabel=='已通过'||row.orderStatusLabel=='已驳回'){
                return this.$message.error('已审批，请勿重复审批')
            }
            this.$http.post(
                this.ports+'/produceOrderAps/getDetailByVoucherNo',
                {voucherNo:row.voucherNo},
                data=>{
                    console.log(data)
                    this.form={
                        orderSourceId:data.orderSourceId,
                        customNo:data.customNo,
                        orderNo:data.orderNo,
                        firstDate:data.firstDate,
                        orgId:data.orgId,
                        voucherDate:data.voucherDate,
                        insideOrder:data.insideOrder,
                        id:row.id,
                        voucherNo:row.voucherNo
                    }
                    // if(dat)
                    this.detailList=data.produceOrderDetailApsDtos
                    this.toastConfig.dialogVisible=true
                },
                this
            )
        },
        unSubmit(){
            this.$http.post(
                this.ports+'/produceOrderAps/audit',
                {voucherNo: this.form.voucherNo,isPass:false},
                data=>{
                    this.toastConfig.dialogVisible=false
                    this.getList()
                    this.$message({
                        type:'success',
                        message:'审批成功'
                    })
                },
                this
            )
        },
        handlerSubmit(){
            this.$http.post(
                this.ports+'/produceOrderAps/audit',
                {voucherNo: this.form.voucherNo,isPass:true},
                data=>{
                    this.toastConfig.dialogVisible=false
                    this.$message({
                        type:'success',
                        message:'审批成功'
                    })
                    this.getList()
                },
                this
            )
        },
        search(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        pageChange(){
            this.getList()
        },
        getList(){
            var param=Object.assign({},this.searchData)
            if(param.dateTime){
                param.voucherDateFrom=param.dateTime[0];
                param.voucherDateTo=param.dateTime[1]
            }
            delete param.dateTime
            this.$http.post(
                this.ports+'/produceOrderAps/list',
                {
                    ...param,
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize
                },
                data=>{
                    this.tableData=data.items
                    this.tableConfig.total=data.numRows
                },
                this
            )
        }
    },
    mounted(){
        this.getList()
        this.$http.post(
            this.ports+'/custom/listInCrm',
            {},
            data=>{
                this.customs=data
            },
            this
        )
        this.$http.post(
            this.ports+'/casOrg/listInHcm',
            {},
            data=>{ 
                console.log(data)
                this.orgList=data
            },
            this
        )
        this.$http.post(
            this.ports+'/productStorePlm/listForQueryCriteria',
            {},
            data=>{
                this.productList=data
            },
            this
        )
    }
}
</script>
<style lang="less">
    .technology{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




