<template>
    <div class="sendEmail">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @Cross="Cross"></my-table>
     <my-toast :config="toastConfig" @cancel="toastConfig.dialogVisible=false">
            <el-form :model="it" v-for="(it,index) in form1" :key="index" ref="ruleForm" label-width="100px" >
    <el-row>
                        <el-col :span="4">
                        <el-form-item>
                         {{it.crafworkName}}
                        </el-form-item>
                    </el-col>
                     <el-col :span="16">
                      <el-form-item >
  <img v-for="item in it.pictureUrls" :src="item" style="height:100px;max-width:100px;padding-right:20px;" >
      </el-form-item>
                    </el-col>
                   <el-col :span="4" class="detailMessage">
                      <p>
                        小计：{{it.price}}
                      </p>
                      <p>
                        完工时间：{{it.okDatetime}}
                      </p>
                      </el-col> 
                </el-row>  
                  </el-form>
                  <el-form ref="ruleForm" label-width="100px"  >
                    <el-row :gutter="18">
                  <el-col :span="6">
                  <el-form-item>合计</el-form-item>
                    </el-col>
                  <el-col :span="8 " :offset="8">
                  <el-form-item>金额:{{form2.total}}</el-form-item>
                    </el-col>
                  </el-row>  
            </el-form>
        </my-toast>
        <my-toast :data="tableData" :config="detailConfig" @cancel="detailConfig.dialogVisible=false" >
            <el-form :model="viewForm" ref="ruleForm" label-width="100px" >
                <el-row>
                    <el-col :span="24">
                      <div class="xcyBlock" v-for="(item,index) in test1.taskFinishedMesDtos">
                        <div class="xcypic">
                          <img :src="item.picture" :key="index"   @click="Cross1(index)">
                        </div>
                        
                        <p style="text-align:center"><span label-width="150px"> {{item.prodName}}×{{item.amount}}</span></p>
                      </div>
                         
                    </el-form-item> 
                    </el-col>
                </el-row>             
    <el-row style="margin-left: 150px;">
          
           </el-row>         
                <el-row>
                  <el-col :span="6">
                  <el-form-item>合计</el-form-item>
                    </el-col>
                  <el-col :span="8">
                  <el-form-item>合计数量:{{form.sum}}</el-form-item>
                    </el-col>
                  <el-col :span="8">
                  <el-form-item>金额:{{form.sumTatal}}</el-form-item>
                    </el-col>
                  </el-row>        
                      </el-form>
        </my-toast>
    </div>
</template>
<script>
import { mapState } from "vuex";
import rule from "../../assets/js/rule.js";
export default {
  data() {
    return {
      one:[],
      test1: {},
      test2: {},
      pictureUrls: [],
      rules: {
        customMess: rule.require("string", "客户信息不能为空"),
        orderNo: rule.require("string", "订单号不能为空"),
        typeId: rule.require("string", "请选择产品分类")
      },
      searchData: {},
      searchConfig: [
        [
          {
            label: "客户信息",
            field: "customMess",
            type: "input",
            placeholder: "请输入产品信息"
          },
          {
            label: "订单号",
            field: "orderNo",
            type: "select",
            placeholder: "请选择产品编码",
            childrens: [],
            prop: {
              label: "orderNo",
              value: "orderNo"
            }
          }
        ]
      ],
      tableData: [],
      tableConfig: {
        isHigh: false,
        isSelection: true,
        align: "center",
        isPage: true,
        pageNo: 1,
        pageSize: 20,
        total: 0,
        currentSelectArr: [],
        colConfig: [
          {
            fixed: false,
            field: "customMess",
            width: "",
            isSort: false,
            isResize: true,
            label: "客户信息",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            field: "empName",
            width: "",
            isSort: false,
            isResize: true,
            label: "交图人",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            field: "invoiceNo",
            width: "",
            isSort: false,
            isResize: true,
            label: "发票号码",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            field: "orderNo",
            width: "",
            isSort: false,
            isResize: true,
            label: "订单号",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            field: "total",
            width: "",
            isSort: false,
            isResize: true,
            label: "总价",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            field: "createTime",
            width: "",
            isSort: false,
            isResize: true,
            label: "发送时间",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            field: "remark",
            width: "",
            isSort: false,
            isResize: true,
            label: "备注",
            align: "center",
            type: "text"
          },
          {
            fixed: false,
            width: "150",
            isSort: false,
            isResize: true,
            label: "操作",
            align: "center",
            type: "button",
            btnData: [
              {
                name: "交图明细",
                method: "Cross"
              }
            ]
          }
        ]
      },
      toastConfig: {
        title: "产品明细", //弹窗标题
        dialogVisible: false, //弹窗显示
        width: "1200px", //弹窗宽
        btnData: [
          {
            isShow: true,
            disabled: false,
            name: "关闭页面", //按钮名
            method: "cancel" //回调函数
          }
        ]
      },
      form: {
        customMess: "",
        empName: "",
        invoiceNo: "",
        total: "",
        remark: "",
        orderNo: "",
        typeId: "",
        createTime: ""
      },
      form1: {
        crafworkName: "",
        empName: "",
        invoiceNo: "",
        total: "",
        remark: "",
        orderNo: "",
        typeId: "",
        createTime: ""
      },
      form2: {
        crafworkName: "",
        empName: "",
        invoiceNo: "",
        total: "",
        remark: "",
        orderNo: "",
        typeId: "",
        createTime: ""
      },
      viewForm: {},
      detailConfig: {
        title: "交图明细", //弹窗标题
        dialogVisible: false, //弹窗显示
        width: "752px", //弹窗宽
        btnData: [
          {
            isShow: true,
            disabled: false,
            bgColor: "dialog-deep-blue",
            name: "关闭窗口", //按钮名
            method: "cancel" //回调函数
          }
        ]
      }
    };
  },
  computed: {
    ...mapState(["ports", "prodTypeList"])
  },

  methods: {
    getList() {
      this.$http.post(
        this.ports + "/deliveryProd/deliPage",
        {
          pageNo: this.tableConfig.pageNo,
          pageSize: this.tableConfig.pageSize,
          ...this.searchData
        },
        data => {
          this.tableData = data.items;
          this.tableConfig.total = data.numRows;
        },
        this
      );
    },
    pageChange() {
      this.getList();
    },
    search() {
      this.tableConfig.pageNo = 1;
      this.tableConfig.pageSize = 20;
      this.getList();
    },
    Cross(row, data) {
      this.$http.post(
        this.ports + "/deliDetl/orderDetail",
        {
          orderNo: row.orderNo
        },
        data => {
          // console.log(data,'/*/*/*/*/*/*/*')
          // for(let i of data.taskFinishedMesDtos){
          //   console.log(i,';;;;;;;;;;;;;;;;;;')
          // }
          this.test1 = data;
          console.log(this.test1.taskFinishedMesDtos,"888888")
          this.form = {
            orderNo: data.taskFinishedMesDtos[0].orderNo,
            sum: data.sum,
            sumTatal: data.sumTatal,
            amount: data.taskFinishedMesDtos[0].amount,
            unit: data.taskFinishedMesDtos[0].unit,
            prodName: data.taskFinishedMesDtos[0].prodName,
            picture: data.taskFinishedMesDtos[0].picture,
            waresNo: data.taskFinishedMesDtos[0].waresNo,
            pictures: Object.assign([], data),
            fileList: Object.assign([], data)
          };
          this.detailConfig.dialogVisible = true;
        },
        this
      );
    },
    Cross1(index) {
      var data=this.test1.taskFinishedMesDtos[index]
  //     data=this.test1.taskFinishedMesDtos
  //     // console.log(this.test1.taskFinishedMesDtos,'**************')
  //  data.forEach(item=>{
  //       this.one=item
  //       console.log(item,data,'**/*/*/*/*/*/*/*/*s')
  //     } )
      this.$http.post(
        this.ports + "/deliDetl/prodDelivery",
        {
          orderNo: data.orderNo,
          waresNo: data.waresNo
        },
      
        data => {
          this.test2 = data;
          console.log(data.pictMess, data, "-----");
          this.form1 = [];
          data.pictMess.forEach(value => {
            this.form1.push({
              crafworkName: value.crafworkName,
              orderNo: value.orderNo,
              waresNo: value.waresNo,
              amount: value.amount,
              pictureUrls: value.pictureUrls,
              unit: value.unit,
              price: value.price,
              total: data.total,
              sumUnit: value.sumUnit,
              sumAmount: value.sumAmount,
              okDatetime: value.okDatetime
            });
          });

          this.form2 = {
            total: data.total,
            sumAmount: data.sumAmount
          };
          this.toastConfig.dialogVisible = true;
        },
        this
      );
    }
  },
  mounted() {
    this.getList(); //获得表格数据
    this.$http.post(
      //下拉框
      this.ports + "/deliveryProd/findOrderNo",
      {},
      data => {
        this.searchConfig[0][1].childrens = data;
        this.productList = data;
      },
      this
    );
  }
};
</script>
<style lang="less">
.sendEmail {
  padding: 20px;
  background: #fff;
  .detailMessage {
    p {
      padding: 5px 0px;
    }
  }
  .my-list {
    padding: 0 20px;
    .template {
      display: flex;
      .text {
        flex: 1;
      }
      .operate {
        width: 100px;
        padding-left: 30px;
        font-size: 14px;
      }
      .span {
        margin: 0;
      }
    }
  }
  .xcyBlock{
           width:100px;
           float:left;
           margin:10px
  }
  .xcypic{
           width:100px;
           height:100px;
           overflow:hidden;
           position:relative;
           border:solid #eee 1px
  }
  .xcypic img{
    width:100%; 
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%)
  }
  h4 {
    font-size: 14px;
    font-family: MicrosoftYaHei;
    color: rgba(42, 147, 255, 1);
    line-height: 30px;
    height: 30px;
    border-bottom: 1px solid #e5e5e5;
    margin: 20px;
  }
}
</style>




