<template>
    <!-- 路由出口 -->
    <div class="app-view">
      <router-view />
      <!-- <loading v-show="getIsLoading"></loading> -->
    </div>
</template>
<script>
import { mapMutations,mapGetters } from "vuex";
export default {
  name: 'app',
  data () {
    return {

    }
  },
  methods: {
    ...mapMutations(["setUserImg", "setUserName",'setCompanyName','setCompanyEnglish','setCompanyLogo'])
  },
  props:['ports', "userName", "userImg","companyName","companyEnglish","companyLogo","menuList"],
  created(){
    sessionStorage.setItem("ports",this.ports)
    this.$store.commit('set_ports',this.ports) 
    this.setUserImg(this.userImg);
    this.setUserName(this.userName);
    this.setCompanyName(this.companyName);
    this.setCompanyEnglish(this.companyEnglish);
    this.setCompanyLogo(this.companyLogo);  
    this.$router.push({ path:'/index'})
    this.$store.commit("setList", this.menuList);
  },
  mounted(){
    // 请求人员列表    
    this.log('12123123123123123')
  }
}
</script>
<style lang="less">
    .model{
        display: flex;
        flex-direction: column;
        height: 100%;
        .tags-content{
            >div{
                margin-right: 15px;
                float: left;
                position: relative;
                height: 20px;
                padding: 0px 10px;
                margin-bottom: 5px;
                font-size:14px;
                font-family:MicrosoftYaHei;
                &.noDefault{
                    color:rgba(42,147,255,1);
                }
                >i{
                    position: absolute;
                    top: 5px;
                    right: 0;
                    height: 14px;
                    cursor: pointer;
                }
            }
        }
        .doc-top{
            height: 40px;
            line-height: 40px;
            border-bottom: #E5E5E5 1px solid;
            padding: 20px;
            >button{
                margin-left: 24px;
            }
            .search {
                margin-left: 24px;
                .el-input-group__append{
                    background: #409eff;
                    border: none;
                    span{
                        color:#fff;
                    }
                }
            }
            img{
                margin: 0 10px 0 0px;
            }
            span{
                font-family:MicrosoftYaHei;
                color:rgba(51,51,51,1);
            }
        }
        .doc-content{
            flex:1;
            display: flex;
            flex-direction: column;
            padding: 0 32px;
            .doc-content-title{
                height: 60px;
                line-height: 60px;
                border-bottom:1px solid #EEEEEE;
                .el-breadcrumb{
                    height: 60px;
                    line-height: 60px;
                    font-size: 18px;
                }
            }
            .doc-content-container{
                flex:1;
                overflow: auto;
            }
        }
    }
</style>


