<template>
<div class="seeUpLoad">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
    <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @upload="upload" dialogVisible:false></my-table>
       <my-toast :config="toastConfig" @close="toastConfig.dialogVisible=false" >
            <el-form :model="form" ref="ruleForm" label-width="100px" > 
                <el-row>
                <el-col :span='8'>
                    <el-form-item label="销售订单号">
                        {{form.orderNo}}
                    </el-form-item>
                    </el-col>
                    <el-col :span='8'>
                    <el-form-item label="客户信息">
                        {{form.customName}}
                    </el-form-item>
                    </el-col>
                    <el-col :span='8'>
                    <el-form-item label="产品信息">
                        {{form.prodName}}
                        </el-form-item>
                    </el-col>
                      </el-row>
                    <el-row>
                            <el-col :span='8'>
                    <el-form-item label="工艺名称">
                        {{form.crafworkName}}
                        </el-form-item>
                    </el-col>
                    <el-col :span='8'>
                    <el-form-item label="图片提交人">
                        {{form.createBy}}
                    </el-form-item>
                    </el-col>
                    <el-col :span='8'>
                    <el-form-item label="上传日期">
                        {{form.createTime}}
                    </el-form-item>
                    </el-col>
                </el-row>   
                <el-row>
                <el-col style="padding-left:100px;padding-right:100px">
                     <div style="width:100px;margin:10px;border:1px solid #666;float:left;position:relative;height:100px;overflow:hidden;" @dblclick="centerDialogVisible = true;currentImg=item" v-for="(item,index) in form.pictures" :key="index">
                      <img style="width:100%;position:absolute;left:50%;top:50%;transform:translate(-50%,-50%)" :src="item"/>
                    </div>
                   
                    </el-col>
                </el-row>
        
                </el-form>
            <!-- <upload style="padding:0 20px;" :form="form"></upload> -->
            
        </my-toast>
        <el-dialog :visible.sync="centerDialogVisible" width="900px">
          <div >
            <div class="no-scroll" style="text-align: center;width: 800px;height: 500px;position: relative;overflow: auto;margin:0 auto;">
              <img :src="currentImg" style="position: absolute;left: 50%;top: 50%;transform: translate(-50%,-50%);max-width:100%"> 
            </div>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="centerDialogVisible = false">关 闭</el-button>
          </span>
        </el-dialog>
    </div>
</template>
<script>
import { mapState } from "vuex";
import upload from "../../components/page.vue";
export default {
  data() {
    return {
      centerDialogVisible:false,
      currentImg:'',
      productList: [],
      deleteArr: [],
      selectData: [],
      tableData: [],
      tableConfig: {
        isHigh: false,
        isSelection: false,
        align: "center",
        loadShow: false,
        isPage: true,
        pageNo: 1,
        pageSize: 20,
        total: 0,
        colConfig: [
          //表格字段配置
          {
            field: "orderNo",
            label: "销售订单号",
            type: "text"
          },
          {
            field: "customName",
            label: "客户信息",
            type: "text"
          },
          {
            field: "prodName",
            label: "产品信息",
            type: "text"
          },
          {
            field: "crafworkName",
            label: "工艺名称",
            type: "text"
          },
          {
            field: "createBy",
            label: "图片提交人",
            type: "text"
          },
          {
            field: "createTime",
            label: "上传日期",
            type: "text"
          },
          {
            label: "操作",
            type: "button",
            btnData: [
              {
                name: "查看详情",
                method: "upload"
              }
            ]
          }
        ]
      },
      searchData: {},
      searchConfig: [
        [
          {
            label: "订单编号",
            field: "orderNo",
            type: "select",
            placeholder: "请选择订单编号",
            childrens: [],
            prop: {
              label: "orderNo",
              value: "orderNo"
            }
          },
          {
            label: "客户信息",
            field: "customName",
            type: "input",
            placeholder: "请输入客户信息"
          },
          {
            label: "产品信息",
            field: "prodName",
            type: "input",
            placeholder: "请输入产品信息"
          },
          {
            label: "上传日期",
            field: "createDate",
            type: "daterange"
          }
        ]
      ],
      toastConfig: {
        //弹框配置
        title: "上传资料",
        dialogVisible: false,
        width: "935px",
        btnData: [
          {
            isShow: true,
            name: "关闭弹窗",
            method: "close"
          }
        ]
      },
      form: {
        fileList: []
      }
    };
  },
  computed: {
    ...mapState(["ports"])
  },
  components: {
    upload
  },
  mounted() {
    this.getData(); //获得表格数据
    this.$http.post(
      //下拉框
      this.ports + "/processPictMes/optionInsideOrder",
      {},
      data => {
        console.log(data, "111111");
        this.searchConfig[0][0].childrens = data;
        this.productList = data;
      },
      this
    );
  },
  methods: {
    upload(row, index) {
      this.$http.post(
        this.ports + "/processPictMes/findAllUploadPic",
        {
          id:row.id,
          orderNo: row.orderNo,
          prodNo: row.prodNo,
          crafworkId: row.crafworkId,
          customNo: row.customNo
        },
        data => {
          console.log(data);
          this.form = {
            orderNo: row.orderNo,
            prodName: row.prodName,
            prodNo: row.prodNo,
            crafworkName: row.crafworkName,
            crafworkId: row.crafworkId,
            customName: row.customName,
            customNo: row.customNo,
            createBy: row.createBy,
            createTime: row.createTime,
            empNo: row.empNo,
            pictures: Object.assign([], data),
            fileList: Object.assign([], data)
          };
          this.toastConfig.dialogVisible = true;
        },
        this
      );
    },
    close() {
      this.$http.post(
        this.ports + "/crafworkApprove/approveStatus",
        {
          id: this.form.id,
          cusIsOk: 3
        },
        data => {
          this.getData();
          this.toastConfig.dialogVisible = false;
        },
        this
      );
    },
    getData() {
      let param = JSON.parse(JSON.stringify(this.searchData));
      if (this.searchData.createDate) {
        param["startDate"] = param.createDate[0];
        param["endDate"] = param.createDate[1];
        delete param.createDate;
      }
      for (let i in param) {
        if (!param[i]) {
          delete param[i];
        }
      }
      this.$http.post(
        this.ports + "/processPictMes/pageAfterUpload",
        {
          pageNo: this.tableConfig.pageNo,
          pageSize: this.tableConfig.pageSize,
          ...param
        },
        data => {
          this.tableData = data.items;
          this.tableConfig.total = data.numRows;
        },
        this
      );
    },
    pageChange() {
      this.getData();
    },
    search() {
      this.tableConfig.pageNo = 1;
      this.tableConfig.pageSize = 20;
      this.getData();
    }
  }
};
</script>
<style lang="less">
    .seeUpLoad{
       padding:20px;
       background:#fff;
        .imgList{
            padding:0 20px;
            >div{
                width: 100px;
                height: 100px;
                position: relative;
                overflow: hidden;
                float: left;
                margin: 0px 10px;
                >img{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 100%;
                    transform: translate(-50%,-50%)
                }
            }
        }
    }
</style>


