import http from "../../api/base"

const getPort = ()=>{
	return sessionStorage.getItem("ports")
}
// 获取订单信息
export const findByMes = (data)=>{
	http.post(getPort() + '/taskFinishedMes/findByMes', data.params, data.success,data.Vue)
}
export const getDropOrderMes = (data)=>{
	http.post(getPort() + '/produceOrderAps/getOrder', data.params, data.success,data.Vue)
}
//任务交工分页详情
export const pictMessDetail = (data)=>{
	http.post(getPort() + '/pictMess/pictDetail', data.params, data.success,data.Vue)
} 

export const accomplish = (data)=>{
	http.post(getPort() + '/pictMess/accomplish', data.params, data.success,data.Vue)
} 
  
export const uploadPath = "http://61.153.187.80:8081/fileSystem/uploadFile";


