import http from "../../api/base"

const getPort = ()=>{
	return sessionStorage.getItem("ports")
}
// 人员信息
export const deliveryProdDeliPage = (data)=>{
	http.post(getPort() + '/deliveryProd/findCus', data.params, data.success,data.Vue)
}
// 邮箱信息
export const deliveryProdFindEmail = (data)=>{
	http.post(getPort() + '/deliveryProd/findEmail', data.params, data.success,data.Vue)
}

export const deliveryProdFindOrder = (data)=>{
	http.post(getPort() + '/deliveryProd/findOrder', data.params, data.success,data.Vue)
}
// export const deliveryProdOrderDetail = (data)=>{
