export const deliveryProdOrderDetail = (data)=>{
	http.post(getPort() + '/deliDetl/orderDetail', data.params, data.success,data.Vue)
}

export const deliveryProdSendMess = (data)=>{
	http.post(getPort() + '/deliveryProd/sendMess', data.params, data.success,data.Vue)
}

export const deliveryProdGetUrl = (data)=>{
	http.post(getPort() + '/deliveryProd/getUrl', data.params, data.success,data.Vue)
}



