<template>
    <div class="product">
        <el-tabs v-model="activeName" type="border-card">
            <el-tab-pane label="产品库" name="first">
                <product-library v-if="activeName=='first'"></product-library>
            </el-tab-pane>
            <el-tab-pane label="分类设置" name="second">
                <set-type v-show="activeName=='second'"></set-type>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<style lang="less">
    .product{
        margin-top: 20px;
    }
</style>
<script>
import productLibrary from './productLibrary.vue'
import setType from './setType.vue'
export default {
    data(){
        return{
            activeName:'first'
        }
    },
    components:{
        setType,
        productLibrary
    }
}
</script>




