:fixed="item.fixed" //列是否固定在左侧或者右侧
:prop="item.field" //对应列内容的字段名
:width="item.width" //对应列的宽度
:sortable="item.isSort" //排序
:resizable="item.isResize" //对应列是否可以通过拖动改变宽度
:show-overflow-tooltip="true" //当内容过长被隐藏时显示tooltip
:label="item.label" //标头标题
:align="config.align" //文本对齐方式
<template>
  <div class="myTable">
       <!-- 交互按钮 -->
       <!-- <toolbar @click="toolbarBack" 
                :toolbarConfig="config.toolbarConfig">
                <slot></slot>
      </toolbar> -->
       <!-- 表格 -->
       <el-table :data="data"
                 @selection-change="select"
                 @select-all="select"
                 @row-dblclick="rowDblclick"
                 :max-height="480"
                 :highlight-current-row="config.isHigh">
         <!-- 是否勾选 -->
          <el-table-column
            v-if="config.isSelection"
            type="selection"
            width="50"
            :align="config.align"
            :disabled="data.length<0">
          </el-table-column>
           <el-table-column
            type="index"
            label="序号"
            :align="config.align"
            v-if="config.isOrder"
            width="100">
          </el-table-column>
         <!-- 表格配置 -->
         <el-table-column v-for="(item,index) in config.colConfig" 
                          :key="index"
                          :fixed="item.fixed" 
                          :prop="item.field"
                          :width="item.width"
                          :sortable="item.isSort"
                          :resizable="item.isResize"
                          :show-overflow-tooltip="true"
                          :label="item.label"
                          :align="config.align"
                          >
                          <!-- 自定义表格内容 -->
                          <template slot-scope="scope">
                            <!-- 普通文本 -->
                            <span v-if="item.type === 'text'" :style="{'color':item.colorFun?item.colorFun(scope.row[item.field]):'#666'}">{{scope.row[item.field]}}</span>
                            <!-- 输入框 -->
                            <el-input v-else-if="item.type === 'input'" v-model="scope.row[item.field]" size="mini"></el-input>
                            <!-- 下拉菜单 -->
                            <el-select v-else-if="item.type === 'select'" v-model="scope.row[item.field]" clearable placeholder="请选择" size="mini">
                                <el-option
                                  v-for="_item in scope.row.selectData"
                                  :key="_item.value"
                                  :label="_item.label"
                                  :value="_item.value">
                                </el-option>
                              </el-select>
                              <el-button type="text" v-else-if="item.type=='button'" :key="index" :style="{'color':_item.color}" v-for="(_item,index) in item.btnData" @click="handlerClick(scope.row,_item.method,scope.$index)">{{_item.name}}</el-button>
                            <!-- 未完待续 -->
                              <span v-else-if="item.type=='qw_status'"><span class="qw_status_on" v-if="scope.row[item.field]=='进行中'"></span> {{scope.row[item.field]}}</span>
                          </template>
          </el-table-column>
       </el-table>
       <!-- 分页 -->
        <div class="my-pagination">
            <el-pagination v-if="config.isPage" 
                           @size-change="handleSizeChange" 
                           @current-change="handleCurrentChange" 
                           :current-page="config.pageNo" 
                           :page-sizes="[20, 40, 60, 80]"
                           :page-size="config.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="config.total">
            </el-pagination>
        </div>
        <!-- loading -->
        <load v-show="config.loadShow"></load>
  </div>
</template>


<script>
export default {
  props: ["config", "data"],
  data(){
    return{
     }
   },
  methods:{
    handleSizeChange(val){//每页数量变化
      if(this.config.isLoading){
        this.config.loadShow = true;//加载
      }
      this.config.pageSize = val;
      this.$emit("pageChange");
    },
    handleCurrentChange(val){//页码变化
      if(this.config.isLoading){
        this.config.loadShow = true;//加载
      }
      this.config.pageNo = val;
      this.$emit("pageChange");
    },
    select(val){//表格Checkbox勾选触发
        this.$emit('select',val)
    },
    rowDblclick(val){//表格单行双击
       this.$emit('rowDblclick',val);
    },
    handlerClick(val,type,index){
      this.$emit(type,val,index)
    }
  }
};
</script>
<style lang="less">
 @colPading:8px 0;
 @theadColor:#666;
 @theadImage:#F6F6F6;
  .myTable{
    position: relative;
    .my-pagination{
      text-align: center;
      margin-top: 10px;
    }
    .el-table td{
      padding:@colPading;
    }
    .el-table thead {
        color: @theadColor;
        font-weight: 500;
        background: @theadImage;
    }
    .el-table th, .el-table tr {
      background-color: rgba(0,0,0,0);
   }
   .my-pagination{
     .el-input__icon{
       line-height: 0;
     }
   }
    .qw_status_on{
      display: inline-block;
      width: 10px;
      height: 10px;
      background: #2A93FF;
      border-radius:50%;
    }
  }
</style>




