<template>
    <div class="setType">
        <toolbar 
            :toolbarConfig="toolbarConfig"
            @handlerAdd="handlerAdd"
        ></toolbar>
         <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
            label="分类名称"
            align="center"
            >
            <template slot-scope="scope">
                <span class="text" style="margin-left: 10px" v-if="!showInputList[scope.$index]">{{ scope.row.typeDesc }}</span>
                <el-input class="editInput"  v-model="scope.row.typeDesc" placeholder="请输入分类名称" style="width:180px;" v-if="showInputList[scope.$index]" size="mini"></el-input>
            </template>
            </el-table-column>
            <el-table-column label="操作" align="center"  width="180px">
            <template slot-scope="scope">
                <el-button
                size="mini"
                type="text"
                v-if="!showInputList[scope.$index]"
                class="editBtn"
                @click="handlerEdit($event, scope.$index, scope.row)">编辑</el-button>
                <el-button
                size="mini"
                v-if="showInputList[scope.$index]"
                type="text"
                @click="handlerEditSuccess($event, scope.$index, scope.row)">保存</el-button>
                <el-button
                size="mini"
                type="text"
                @click="handleDelete($event, scope.$index, scope.row)" style="color:#F6463B;margin-left:30px;">删除</el-button>
            </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    data(){
        return{
            showInputList:[],
            toolbarConfig:{
                custom:[
                    {
                        icon: "plus",
                        type: "primary",
                        method: "handlerAdd",
                        name: "新增"
                    },

                ],
                btnStatus:[false]
            },
            tableData:[{typeDesc:123}]
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        handlerAdd(){
            this.$prompt('请输入分类名称', '新增分类', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                this.$http.post(
                    this.ports+'/prodType/add',
                    {typeDesc:value},
                    data=>{
                        this.$message({
                            type: 'success',
                            message: '新增成功'
                        });  
                        this.showInputList.unshift(false)
                        this.tableData.unshift(data)
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                });       
            });
        },
        handlerEdit(e,index,data){
            this.$set(this.showInputList,index,true)
            // this.showInputList[index]=true
        },
        handleDelete(e,index,data){
            this.$http.post(
                this.ports+'/prodType/remove',
                {ids:data.id},
                res=>{
                    this.showInputList.splice(index,1)
                    this.tableData.splice(index,1)   
                },
                this
            )
        },
        handlerEditSuccess(e,index,data){
            this.$http.post(
                this.ports+'/prodType/update',
                data,
                res=>{
                    console.log(res)
                    this.$set(this.showInputList,index,false)
                    // this.showInputList[index]=false
                    this.tableData[index]=res
                },
                this
            )
        },
        getList(){
            this.$http.post(
                this.ports+'/prodType/page',
                {pageSize:2000,pageNo:1},
                data=>{
                    this.tableData=data.items
                    for(let d of data.items){
                        this.showInputList.push(false)
                    }
                    this.$store.commit('setProdTypeList',data.items)
                },
                this
            )
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    @colPading:8px 0;
    @theadColor:#666;
    @theadImage:#F6F6F6;
    .setType{
        .el-table td{
            padding:@colPading;
        }
        .el-table thead {
            color: @theadColor;
            font-weight: 500;
            background: @theadImage;
        }
        .el-table th, .el-table tr {
            background-color: rgba(0,0,0,0);
        }
        
    }
</style>





