<template>
    <div class="customerUploadData">
        <my-table 
        :data="tableData" 
        :config="tableConfig"
        @pageChange="pageChange"
        @upload="upload"
        ></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="handlerCancel" >
            <el-form :model="form"  ref="ruleForm" label-width="100px" style="margin-right:30px;">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="销售订单号:">
                            {{form.orderNo}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="产品信息:">
                            {{form.prodName}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="工艺信息:">
                            {{form.crafworkName}}
                        </el-form-item>
                    </el-col>
                </el-row>
                <!-- <el-row>
                    <el-col :span="8">
                        <el-form-item label="图片提交人:">
                            {{form.empName}}
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="图片上传时间:">
                            {{form.createTime}}
                        </el-form-item>
                    </el-col>
                </el-row> -->
            </el-form>
            <upload style="padding:0 20px;" :form="form"></upload>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import upload from '../../components/page.vue'
export default {
    data(){
        return{
            // 要删除的参数
            deleteArr:[],
            // 当前表格选中项
            selectData:[],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: false,
                align: "center",
                loadShow:false,
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "orderNo",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "销售订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "createTime",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "上传时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "200px",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'上传资料',
                                method:'upload'
                            }
                        ]
                    }
                ]
            },
            toastConfig:{
                title:'上传资料',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'900px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'提交',//按钮名
                    method:'submit',//回调函数
                }]
            },
            form:{
                fileList:[]
            },
            jobs:[],
            // 设置参数
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    components:{
        upload
    },
    methods:{
        // 评审
        upload(row,index){
            this.$http.post(
                this.ports+'/processPictMes/findAllUploadPic',
                {orderNo:row.orderNo,prodNo:row.prodNo,crafworkId:row.crafworkId,id:row.id},
                data=>{
                    console.log(data)
                    this.form={
                        orderNo:row.orderNo,
                        prodName:row.prodName,
                        prodNo:row.prodNo,
                        crafworkName:row.crafworkName,
                        crafworkId:row.crafworkId,
                        pictures:Object.assign([],data),
                        fileList:Object.assign([],data),
                        id:row.id
                    }
                    this.toastConfig.dialogVisible=true
                },
                this
            )
            
        },
        submit(){
            var param=Object.assign({},this.form)
            param.pictures=param.pictures.join(',')
            delete param.fileList
            this.$http.post(
                this.ports+'/processPictMes/uploadFile',
                param,
                data=>{
                    this.getList()
                    this.toastConfig.dialogVisible=false;
                    this.$message({
                        type:'success',
                        message:'资料上传成功'
                    })
                },
                this
            )
        },
        handlerCancel(){
            this.toastConfig.dialogVisible=false;
            this.$message({
                type:'success',
                message:'已取消上传'
            })
            // this.$http.post(
            //     this.ports+'/crafworkApprove/approveStatus',
            //     {
            //         id:this.form.id,
            //         cusIsOk:3
            //     },
            //     data=>{
            //         this.getList()
                    
            //     },
            //     this
            // )
        },
        getList(){
            this.$http.post(
                this.ports+'/processPictMes/page',
                {
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        pageChange(){
            this.getList()
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    .customerUploadData{
       padding:20px;
       background:#fff;
        .imgList{
            padding:0 20px;
            >div{
                width: 100px;
                height: 100px;
                position: relative;
                overflow: hidden;
                float: left;
                margin: 0px 10px;
                >img{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 100%;
                    transform: translate(-50%,-50%)
                }
            }
        }
    }
</style>




