<template>
    <div class="technology">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <toolbar 
            :toolbarConfig="toolbarConfig"
            @handlerAdd="handlerAdd"
            @handlerEdit="handlerEdit"
            @handlerDel="handlerDel"
        ></toolbar>
        <my-table 
        :data="tableData" 
        :config="tableConfig"
        @pageChange="pageChange"
        @set="set"
        @select="select"></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()" @savaAgain="savaAgain">
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;"  v-if='toastConfig.dialogVisible == true'>
                <el-form-item label="工艺名称" prop="crafworkName">
                    <el-input v-model="form.crafworkName" size="medium"></el-input>
                </el-form-item>
                <el-form-item label="工艺岗位" prop="quartersEmp">
                    <el-select v-model="form.quartersEmp" placeholder="请选择工艺岗位" size="medium" style="width:100%">
                      <el-option v-for="(job,index) in jobs" :key="index" :label="job.quartersDesc" :value="job.quartersDesc"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="标准工时" prop="actHours">
                    <el-input-number :min="0" :controls="false" style="width:100%" v-model="form.actHours" size="medium"></el-input-number>
                </el-form-item>
                <el-form-item label="计量单位" prop="unit">
                    <el-input v-model="form.unit" size="medium"></el-input>
                </el-form-item>
            </el-form>
        </my-toast>
        <my-toast :config="paramSet" @cancel="paramSet.dialogVisible=false" @submit="paramSubmit">
            <div class="my-list">
                <el-form :model="paramForm"  label-width="100px" style="margin-right:30px;">
                    <el-form-item label="工艺参数名">
                        <div v-for="(item,index) in paramForm.list" :key="index" class="template">
                            <div class="text">
                                <el-input v-model="item.paramName" size="medium"></el-input>
                            </div>
                            <div class="operate">
                                <el-button icon="el-icon-plus" type="text"  @click="listAdd(index,item)"></el-button>
                                <el-button icon="el-icon-delete" :disabled="paramForm.list.length==1" @click="paramForm.list.splice(index,1);deleteArr.push(item.id)"  type="text"></el-button>
                            </div>
                        </div>
                    </el-form-item>
                </el-form>
            </div>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            // 要删除的参数
            deleteArr:[],
            // 当前表格选中项
            selectData:[],
            // 校验
            rules:{
                crafworkName:rule.require('string','工艺名称不能为空'),
                quartersEmp:rule.require('string','请选择工艺岗位'),
                actHours:rule.require('number','标准工时不能为空'),
                unit:rule.require('string','计量单位不能为空')
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "工艺名称",
                        field: "crafworkName",
                        type: "input",
                        placeholder: "请输入姓名"
                    }
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                loadShow:false,
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "quartersEmp",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺岗位",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "actHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "标准工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "unit",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计量单位",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'参数设置',
                                method:'set'
                            }
                        ]
                    }
                ]
            },
            toolbarConfig:{
                custom:[
                    {
                        icon: "plus",
                        type: "primary",
                        method: "handlerAdd",
                        name: "新增"
                    },
                    {
                        icon: "edit",
                        type: "",
                        method: "handlerEdit",
                        name: "编辑"
                    },
                    {
                        icon: "delete",
                        type: "",
                        method: "handlerDel",
                        name: "删除"
                    }
                ],
                btnStatus:[false,true,true]
            },
            toastConfig:{
                title:'新增工艺',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'500px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    name:'确定并继续',
                    type:'primary',
                    method:'savaAgain',
                    bgColor:'dialog-deep-blue'
                }]
            },
            form:{
                crafworkName:'',
                quartersEmp:'',
                actHours:'',
                unit:''
            },
            jobs:[],
            // 设置参数
            paramForm:{
                list:[],
            },
            paramSet:{
                title:'参数设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'500px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'保存',//按钮名
                    method:'submit',//回调函数
                }]
            },
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        // 参数设置
        set(row,index){
            console.log(row)
            this.$http.post(
                this.ports+'/crafworkStructPlm/findParamByStructId',
                {crafworkId:row.id},
                data=>{
                    // this.paramForm.id=row.id;
                    this.paramForm.list=[]
                    for(let d of data){
                        this.paramForm.list.push({
                            crafworkId:d.crafworkId,
                            id:d.id,
                            paramName:d.paramName
                        })
                    }
                    this.deleteArr=[]
                    // this.paramForm.list=data
                    if(this.paramForm.list.length==0){
                        this.paramForm.list.push({paramName:'',id:null,crafworkId:row.id})
                    }
                    this.paramSet.dialogVisible=true
                    console.log(data,"最新版")
                },
                this
            )
            // this.paramSet.dialogVisible=true
        },
        handlerAdd(){
             this.toastConfig.title='新增工艺'
            this.form={
                crafworkName:'',
                quartersEmp:'',
                actHours:'',
                unit:'',
                id:0
            }
            this.toastConfig.btnData[2].isShow=true
            this.toastConfig.dialogVisible=true
        },
        paramSubmit(){
            this.$http.post_noqs(
                this.ports+'/crafworkStructPlm/updateParam',
                this.paramForm.list,
                data=>{
                    this.$message({
                        type: 'success',
                        message: '参数设置成功!'
                    });
                    // if(this.deleteArr.length>0){
                    //     this.$http.post(
                    //         this.ports+'/crafworkStructPlm/removeParam',
                    //         {ids:this.deleteArr.join(',')},
                    //         data=>{
                    //             this.paramSet.dialogVisible=false
                    //         },  
                    //         this
                    //     )
                    // }else{
                        this.paramSet.dialogVisible=false
                    // }
                },  
                this
            )
        },
        handlerDel(){
            this.$confirm('确认删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var ids=[]
                for(let d of this.selectData){
                    ids.push(d.id)
                }
                this.$http.post(
                    this.ports+'/crafworkStructPlm/remove',
                    {ids:ids.join(',')},
                    data=>{
                        this.getList()
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
                savaAgain(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post(
                        this.ports+'/crafworkStructPlm/add',
                        this.form,
                        data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=true;
                                this.getList()
                            },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
         submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(this.form.id==0){
                        this.$http.post(
                            this.ports+'/crafworkStructPlm/add',
                            this.form,
                            data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.getList()
                                 this.$message({
                                    type: 'success',
                                    message: '新增成功!'
                                });
                            },
                            this
                        )
                    }else{
                        this.$http.post(
                            this.ports+'/crafworkStructPlm/update',
                            this.form,
                            data=>{
                                this.$refs.ruleForm.resetFields()
                                this.toastConfig.dialogVisible=false;
                                this.form={
                                    crafworkName:'',
                                    quartersEmp:'',
                                    actHours:'',
                                    unit:''
                                }
                                this.getList()
                                this.$message({
                                    type: 'success',
                                    message: '编辑成功!'
                                });
                            },
                            this
                        )
                    }
                    
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        select(data){
            this.selectData=data
            if(data.length==1){
                this.toolbarConfig.btnStatus=[false,false,false]
            }else if(data.length==0){
                this.toolbarConfig.btnStatus=[false,true,true]
            }else{
                this.toolbarConfig.btnStatus=[false,true,false]
            }
        },
        listAdd(index,row){
            this.paramForm.list.splice(index+1,0,{paramName:'',id:null,crafworkId:row.crafworkId})
        },
        handlerSearch(){
            this.tableConfig.pageNo=1;
            this.tableConfig.pageSize=20;
            this.getList()
        },
        handlerEdit(){
             this.toastConfig.title='编辑工艺'
            this.form=Object.assign({},this.selectData[0])
            this.toastConfig.btnData[2].isShow=false
            this.toastConfig.dialogVisible=true
        },
        getList(){
            this.$http.post(
                this.ports+'/crafworkStructPlm/page',
                {
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        pageChange(){
            this.getList()
        }
    },
    mounted(){
        this.getList()
        this.$http.post(
            this.ports+'/crafworkStructPlm/optionQuarters',
            {},
            data=>{
                this.jobs=data
            },
            this
        )
    }
}
</script>
<style lang="less">
    .technology{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




