    [] <template>
    <div class="electronicHairMap">
        <el-tabs v-model="activeName" type="border-card">
            <el-tab-pane label="写邮件" name="first">
                <write-email></write-email>
            </el-tab-pane>
            <el-tab-pane label="已发送邮件" name="second">
                <send-email v-if="activeName=='second'"></send-email>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<style lang="less">
    .product{
        margin-top: 20px;
    }
</style>
<script>
import writeEmail from './writeEmail.vue'
import sendEmail from './sendEmail.vue'
export default {
    data(){
        return{
            activeName:'first'
        }
    },
    components:{
         writeEmail,
        sendEmail
       
    }
}
</script>




