
import Vue from 'vue'
import Vuex from 'vuex'
import type from '../assets/js/common.js'
Vue.use(Vuex)

export default new  Vuex.Store({
    state:{
        ports:null,
        isLoading:false,
        companyId:null,
        userId:null,
        userName:'',
        userImg:'',
        companyName:'',
        companyEnglish:'',
        companyLogo:'',
        prodTypeList:[],
        listMenu:[],
        menuList:[],
    },
    mutations:{
        setUserName:(state,data)=>{state.userName = data},
        setUserImg:(state,data)=>{state.userImg = data},
        setCompanyName:(state,data)=>{state.companyName = data},
        setCompanyEnglish:(state,data)=>{state.companyEnglish = data},
        setCompanyLogo:(state,data)=>{state.companyLogo = data},
        print(state,str){//输出日志
             if(state.isLog){
                 console.log(str);//打印
             }
        },
        set_ports:(state,ports)=>{
            state.ports=ports
        },
        setCompanyId:(state,companyId)=>{
            state.companyId=companyId
        },
        setUserId:(state,userId)=>{
            state.userId=userId
        },
        loading:(state)=>{
            state.isLoading=true
        },
        cancelLoad:(state)=>{
            state.isLoading=false
        },
        setProdTypeList:(state,data)=>{
            state.prodTypeList=data
        },
        setList:(state,data)=>{
            var list=JSON.parse(data)
            if(list.length>0){
                state.menuList=type.getTree(list,{}).chlid
                state.listMenu=list
            }else{
                state.menuList=[]
                state.listMenu=[]
            }
            
        },
    },
    getters:{
        getUserName:(state) => state.userName,
        getUserImg:(state) => state.userImg,
        getCompanyName:(state) => state.companyName,
        getCompanyEnglish:(state) => state.companyEnglish,
        getCompanyLogo:(state) => state.companyLogo
    }
})



