<template>
    <div class="writeEmail">
        <el-form :data="tableData" :config="tableConfig" :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="padding-left:30px;" label-position="right" >
            <el-row>
                    <el-col :span="12">
                        <el-form-item label="寄件地址" prop="sendAddress">
                            <el-input placeholder="请输入寄件地址" maxlenth="125"  v-model="form.sendAddress" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
            </el-row>
            <el-row>
                    <el-col :span="12">
                        <el-form-item label="客户信息" prop="customNo" style="position:relative;">
                          <el-select @change="empNoChange" style="width:100%;"
                            v-model="form.customNo"
                            filterable
                            remote
                            reserve-keyword
                            placeholder="请输入关键词"
                            :remote-method="queryEmpInfo"
                            :loading="loading">
                            <el-option
                              v-for="item in dropDown.empList"
                              :key="item.customMess"
                              :label="item.customMess"
                              :value="item.customNo">
                            </el-option>
                          </el-select>
                        </el-form-item> 
                    </el-col>
                       </el-row>
                       <el-row>
                    <el-col :span="12">
                          <el-form-item ref="email" label="电子邮箱" prop="email">
                            <el-select style="width:100%" v-model="form.email" size="medium">
                            <el-option v-for="(item,index) in dropDown.emailList" :key="index" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                      </el-row>
                       <el-row>
                    <el-col :span="12">
                        <el-form-item label="交图人" prop="empNo">
                            <el-input v-model="form.empNo" maxlength="10" placeholder="请输入交图人"></el-input>
                        </el-form-item>
                    </el-col>
                      </el-row>
                       <el-row>
                    <el-col :span="12">
                        <el-form-item label="发票号码" prop="invoiceNo">
                            <el-input placeholder="请输入发票号码" maxlength="20" v-model="form.invoiceNo" size="medium"></el-input>
                        </el-form-item>
                    </el-col>
                      </el-row>
                       <el-row>
                    <el-col :span="12">
                         <el-form-item ref="order" label="订单号" prop="orderNo">
                            <el-select @change="orderNoChange" style="width:100%" v-model="form.orderNo" size="medium">
                            <el-option v-for="(item,index) in dropDown.orderList" :key="index" :label="item.label" :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                      </el-row>
                        <el-row>
                          <el-col :span="12">
                        <el-form-item label="交图明细">
                            <a v-show="form.deliveryUrl" :href="form.deliveryUrl" target="blank">{{form.deliveryUrl}}</a>
                        </el-form-item>
                    </el-col>
                      </el-row>
                      <el-row>
                          <el-col :span="12">
           <el-form-item label="总价" prop="total">
                    <el-input readonly placeholder="订单总价" v-model="form.total"></el-input>   
                <!-- {{form.total}} -->
                </el-form-item>
                    </el-col>
                      </el-row>
                        <el-row>
                          <el-col :span="12">
                        <el-form-item label="备注" prop="remark">
                            <el-input placeholder="请输入备注" maxlength="125" v-model="form.remark" type="textarea" size="medium" ref="sendEmail"></el-input>
                        </el-form-item>
                    </el-col>
                      </el-row>
                       <el-form-item>
                        <el-button type="primary" @click="submit">发送</el-button>
                           </el-form-item>
            </el-form>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
import sendEmail from './sendEmail.vue'
import {
    deliveryProdDeliPage,
    deliveryProdFindEmail,
    deliveryProdFindOrder,
    deliveryProdOrderDetail,
    deliveryProdSendMess
} from "./api.js"
export default {
    data(){
        return{
            loading:false,
            userFocus:false,
            dropDown:{
                empList:[],
                emailList:[],
                orderList:[]
            },
            productList:[],
            tableData: {},
            tableConfig: [
        [
          {
            label: "电子邮箱",
            field: "email",
             filterable:true,
            type: "select",
            placeholder: "请选择订单编号",
            childrens: [],
            prop: {
              label: "email",
              value: "email"
            }
          },
                    {
            label: "客户信息",
            field: "customMess",
            filterable:true,
            type: "input",
            placeholder: "请选择客户信息",
            childrens: [],
            prop: {
              label: "customMess",
              value: "customNo"
            }
          },
        ]
      ],
             form:{
                sendAddress:"",
                customNo:'',
                customName:"",
                email:'',
                empNo:'',
                invoiceNo:"",          //                      发票号码
                orderNo:'',
                deliveryUrl:"",
                total:'',
                prodNo:'',
                remark:'',
                address:'',
                unit:'',
                customMess:'',
                remark:'',
            },
            rules:{
                sendAddress:[{ required: true, message: '此选择为必填项', trigger: ['input',"blur"]}],            //                      寄件地址
                customName:[{ required: true, message: '此选择为必填项', trigger: 'change'}],    //                      客户名称
                customNo:[{ required: true, message: '此选择为必填项', trigger: 'change'}],                  //                      客户编码
                email:[{ required: true, message: '此选择为必填项', trigger: 'change'}],                  //                      邮箱
                empNo:[{ required: true, message: '此选择为必填项', trigger: ['input',"blur"]}],                //                      交图人                 
                invoiceNo:[{ required: true, message: '此选择为必填项', trigger: ['input',"blur"]}],          //                      发票号码
                orderNo:[{ required: true, message: '此选择为必填项', trigger: 'change'}],     //                      订单编号
                deliveryUrl:[{ required: true, message: '此选择为必填项', trigger: 'change'}],     //                      交图明细链接
                total:[{ required: true, message: '此选择为必填项', trigger: 'change'}],                   //                      总价
                remark:[{ required: false, message: '此选择为必填项', trigger: 'change'}],               //                      备注
            },
            viewForm:{},
                  form: {
        fileList: [],
        jobs:[],
      },
        }
    },
    computed:{
        // deliveryUrl:()=>{
        //     return 
        // },
        ...mapState(['ports','prodTypeList'])
    },
    methods:{
        queryEmpInfo(query){
            let params={customMess: query};
            params=this.setAttr(params);
            // let searchData=this.searchData;
            deliveryProdDeliPage({params:params,success:res=>{
                let data=[];
                let temp=res;
                for(let attr in temp){
                    data.push({customMess:temp[attr].customMess,customNo:temp[attr].customNo})
                }
                this.dropDown.empList=data;
            },Vue:this})
        },
        empNoChange(empNo){
            if(/^\d{1,11}$/.test(empNo)){ 
                for(let item of this.dropDown.empList){
                    if(item.customNo==empNo){
                        this.form.customName=item.customMess;                        
                        // setTimeout(()=>{this.$refs.email.resetField()},10)
                        // setTimeout(()=>{this.$refs.order.resetField()},10)
                        this.syncDropDown(empNo);
                        break;
                    }
                }
                
                
            }
        },
        syncDropDown(empNo){
            let params={customNo: empNo};
                params=this.setAttr(params);
                deliveryProdFindEmail({params:params,success:res=>{
                    let data=[];
                    for(let item of res){
                        data.push({label:item.email,value:item.email});
                    }
                    this.dropDown.emailList=data;
                },Vue:this})
                deliveryProdFindOrder({params:params,success:res=>{
                    let data=[];
                    for(let item of res){
                        data.push({label:item.orderNo,value:item.orderNo});
                    }
                    this.dropDown.orderList=data;
                },Vue:this})
        },
        orderNoChange(orderNo){
            let that=this;
            if(orderNo){
                let params={orderNo: orderNo};                
                deliveryProdOrderDetail({params:params,success:res=>{
                    that.$set(that.form,"total",res.sumTatal);
                    that.$set(that.form,"deliveryUrl",res.deliveryUrl);
                },Vue:this})
            }           
        },
         setAttr(data){
            for(let attr in data){
                if(undefined===data[attr]||(null===data[attr])||(""===data[attr])){
                    delete data[attr];
                }
            }
            return  data;
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {

                    let params={
                        ...this.form
                            // orderNo:this.form.orderNo,              //                      订单编号
                            // sendAddress:this.form.sendAddress,      //                      寄件地址
                            // customNo:this.form.customNo,            //                      客户编码
                            // customName:this.form.customName,        //                      客户名称
                            // email:this.form.email,                  //                      邮箱
                            // invoiceNo:this.form.invoiceNo,          //                      发票号码
                            // total:this.form.total,                  //                      总价
                            // remark:this.form.remark,                //                      备注
                            // sendNo:this.form.sendNo,                //                      交图人 
                            // deliveryUrl:this.form.deliveryUrl,      //                      交图明细链接
                    };
                    params=this.setAttr(params);
                    // let searchData=this.searchData;
                    deliveryProdSendMess({params:params,success:res=>{
                        this.$message.success("操作成功！");
                        setTimeout(()=>{
                            for(let attr in this.form){
                                this.form[attr]="";
                            }
                            this.form.deliveryUrl="";
                            this.$refs.ruleForm.clearValidate();
                            this.$refs.ruleForm.resetFields();
                            
                            setTimeout(()=>{
                                
                            },1000)
                        })
                        console.log(this.$refs.ruleForm.getList(),'***********************')
                    },Vue:this})

                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        savaAgain(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post(
                        this.ports+'/productStorePlm/addProduct',
                        this.form,
                        data=>{
                            this.$refs.ruleForm.resetFields()
                            this.form={
                                crafworkName:'',
                                quartersEmp:'',
                                actHours:'',
                                unit:''
                            }
                            this.getList()
                        },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        select(data){
            this.selectData=data
            if(data.length==1){
                this.toolbarConfig.btnStatus=[false,false,false]
            }else if(data.length==0){
                this.toolbarConfig.btnStatus=[false,true,true]
            }else{
                this.toolbarConfig.btnStatus=[false,true,false]
            }
        },
        getList(){
            this.$http.post(
                this.ports+'/productStorePlm/pageProduct',
                {
                    pageNo:this.tableConfig.pageNo,
                    pageSize:this.tableConfig.pageSize,
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        pageChange(){
            this.getList()
        },
        see(data){
            this.viewForm=Object.assign({},data)
            this.detailConfig.dialogVisible=true
        }
    },
     components:{
        sendEmail
    },
    mounted(){
        this.getList()
/*          this.$http.post(
      //下拉框
      this.ports + "/deliveryProd/deliPage",
      {},
      data => {
         this.jobs=data
      },
      this
    ); */
    this.$http.post(
      //下拉框
      this.ports + "/deliveryProd/findEmail",
      {},
      data => {
        this.tableConfig[0][0].childrens = data;
        this.productList = data;
      },
      this
    );
    }
}
</script>
<style lang="less">

    .writeEmail{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
        h4{
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(42,147,255,1);
            line-height:30px;
            height: 30px;
            border-bottom: 1px solid #E5E5E5;
            margin: 20px;
        }
    
        
    }
    .qwIcon.el-icon-circle-close{
        color:#F00;
    }
    .qwIcon.el-icon-circle-check{
        color:#090;
    }
    .qwIcon{
        position:absolute;
        right:0;
        top:10px;
        font-size:20px;
    }
</style>




