<template>
    <div class="tasking">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <toolbar 
            :toolbarConfig="toolbarConfig"
            @setPrice="setPrice"
            
        ></toolbar>
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange" @tasking="tasking"  @select="select"></my-table>
        <my-toast :config="toastConfig" @submit="submit" @cancel="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()" @handleClose="$refs.ruleForm.resetFields()"> 
            <el-form :model="form" :rules="rules" ref="ruleForm" label-width="110px" style="margin:0 20px;">
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="内部订单号" >
                            <span>{{form.insideOrder}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="产品信息" >
                            <span>{{form.prodName}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="工艺名称" >
                            <span>{{form.crafworkName}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="工艺工价" prop="price">
                            <el-input-number :min="0" :controls="false" v-model="form.price" size="small"></el-input-number>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="工艺执行人" prop="doEmpNo">
                            <el-select size="small" v-model="form.doEmpNo" placeholder="">
                              <el-option v-for="item in userList" :key="item.doEmpNo" :label="item.empName" :value="item.empNo"></el-option>
                            </el-select>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="数量" >
                            <span>{{form.amount}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                        <el-form-item label="计划开始时间" prop="planDate">
                            <el-date-picker
                                v-model="form.planDate"
                                type="datetime"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                 size="small"
                                 style="width:100%"
                                placeholder="选择计划开始时间">
                            </el-date-picker>
                        </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="计划工时" prop="planHours">
                            <el-input-number :min="0" v-model="form.planHours" :controls="false" size="small"></el-input-number>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item label="实际开始时间" prop="actDate">
                    <el-date-picker
                        v-model="form.actDate"
                        type="datetime"
                        value-format="yyyy-MM-dd HH:mm:ss"
                            size="small"
                            style="width:100%"
                        placeholder="选择计划开始时间">
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
import {
    getDropOrderMes,
    getInsideOrder
} from './api.js'
export default {
    data(){
        return{
            //工艺执行人列表
            userList:[],
            selectData:[],
            rules:{
                price:rule.require('number','工艺名称不能为空'),
                doEmpNo:rule.require('string','请选择工艺执行人'),
                planDate:rule.require('string','请选择计划开始时间'),
                planHours:rule.require('number','计划工时不能为空'),
                actDate:rule.require('string','请选择实际开始时间'),
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "内部订单号",
                        field: "insideOrder",
                        filterable: true,
                        type: "select",
                        placeholder: "请输入/选择内部订单号",
                        childrens:[]
                    },
                    {
                        label: "产品信息",
                        field: "prodNo",
                        filterable: true,
                        type: "select",
                        placeholder: "请输入/选择产品信息",
                        childrens:[]
                    },
                    {
                        label: "任务状态",
                        field: "status",
                        type: "select",
                        placeholder: "请输入姓名",
                        childrens:[{name:'待派工',value:'0'},{name:'处理中',value:'1'},{name:'已完成',value:'2'},{name:'审核未通过',value:'3'}]
                    },
                ]
            ],
            toolbarConfig:{
                custom:[
                    {
                        // icon: "plus",
                        type: "primary",
                        method: "setPrice",
                        name: "设置工艺价格"
                    }
                ],
                btnStatus:[true]
            },
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "内部订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "price",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺工价",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "doEmpName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺执行人",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "amount",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "数量",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计划开始时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "actDate",
                        width: "120",
                        isSort: false,
                        isResize: true,
                        label: "实际开始时间",
                        align: "center",
                        type: "text"
                    }, 
                    {
                        fixed: false,
                        field: "getTime",
                        isSort: false,
                        isResize: true,
                        label: "领派工时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkStandHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "标准工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计划工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "statusName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "任务状态",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "操作",
                        align: "center",
                        type: "button",
                        btnData:[
                            {
                                name:'任务分配',
                                method:'tasking'
                            }
                        ]
                    }
                ]
            },
            toastConfig:{
                title:'任务分配',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'560px',//弹窗宽
                btnData:[{
                    isShow:true,
                    disabled:false,
                    name:'取消',//按钮名
                    method:'cancel',//回调函数
                },{
                    disabled:false,
                    isShow:true,
                    type:'primary',
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                }]
            },
            form:{

            },
        }
    },
    computed:{
        ...mapState(['ports'])
    },
        created(){
        this.getDropOrderMes();
        this.getList();
    },
    methods:{
        getList(){
            this.$http.post(
                this.ports+'/taskPondMes/listForDistributeTask',
                {
                    pageSize:this.tableConfig.pageSize,
                    pageNo:this.tableConfig.pageNo,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    console.log(this.tableData,'+++++++++++++++++++++++++')
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        handlerSearch(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        setPrice(){
            this.$prompt('工艺工价', '请输入工艺工价', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /^[1-9]\d*(\.\d+)?$/,
                inputErrorMessage: '请输入工艺工价'
            }).then(({ value }) => {
                var ids=[]
                for(let val of this.selectData){
                    ids.push(val.id)
                }
                this.$http.post(
                    this.ports+'/taskPondMes/setTaskPrice',
                    {taskPondIds:ids.join(','),price:value},
                    data=>{
                        this.getList()
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                });       
            });
        },
        select(data){
            this.selectData=data
            if(data.length>=1){
                this.toolbarConfig.btnStatus=[false]
            }else {
                this.toolbarConfig.btnStatus=[true]
            }
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.$http.post_noqs(
                        this.ports+'/taskPondMes/distributeTask',
                        this.form,
                        data=>{
                            this.$refs.ruleForm.resetFields()
                            this.dialogVisible=true
                            this.$message({
                                type:'success',
                                message:'任务分配成功'
                            })
                            this.toastConfig.dialogVisible=false
                            this.getList()
                        },
                        this
                    )
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        tasking(val){
            if(val.status!=0){
                return this.$message.error('任务不可分配，请确认任务是否已被领取或已经分配')
            }
            this.form=Object.assign({},val)
            this.$http.post(
                this.ports+'/empfloderHcm/getByTaskPondId',
                {taskPondId:val.id},
                data=>{
                    console.log(data)
                    this.userList=data
                    this.toastConfig.dialogVisible=true
                },
                this
            )
            
        },
        pageChange(){
            console.log('111')
            this.getList()
        },
                //获取订单信息下拉
        getDropOrderMes(){
            getDropOrderMes(
                {
                    params:{},
                    success:res=>{
                for(let o of res){
                    this.searchConfig[0][1].childrens.push({
                        "name" : o.prodName,"value" : o.prodNo
                    });
                }
            },Vue:this})
        },
        getInsideOrder(){
               getInsideOrder({params:{},success:res=>{
                console.log("res==>",res);
                let tem=[];
                for(let o in res){
                    tem.push({"name" : res[o].insideOrder,"value" : res[o].insideOrder});
                }

                this.searchConfig[0][0].childrens=tem;
            },Vue:this})
        },
    },
    mounted(){
        this.getInsideOrder()
        this.getList()
    }
}
</script>
<style lang="less">
    .tasking{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




